import { useEffect, useState } from "react";
import { fetchPatients } from "../../network";
import { Space } from "antd";
import { DataTable } from "../../../shared/Table";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../../routes";
import {
  PATIENT_COLUMNS,
  PatientEntity,
} from "../../../../entities/patient.entity";

export const ListPatientComponent = () => {
  const router = useHistory();
  const [isLoading, setIsLoading] = useState(true);
  const [patients, setPatients] = useState<PatientEntity[]>([]);

  useEffect(() => {
    fetchPatients().then((data) => {
      if (data.success) {
        setPatients(data.result);
        setIsLoading(false);
      }
    });
  }, []);

  console.log("patient list actions", patients);

  return (
    <div>
      {/* <span className="text-muted">
        nom | date de naissance | formation sanitaires
      </span> */}
      <DataTable
        key={"list-patient-table"}
        loading={isLoading}
        data={patients} // classer par ordre dcroissant
        columns={PATIENT_COLUMNS()}
        filterFunction={(patient: PatientEntity, filterValue: string) =>
          patient.nom.includes(filterValue) ||
          patient.prenom.includes(filterValue) ||
          patient.nom.toLowerCase().includes(filterValue) ||
          patient.prenom.toLowerCase().includes(filterValue) ||
          patient.date_naissance.includes(filterValue)
        }
        buttons={
          <Space>
            <button
              className="btn btn-outline-primary rounded-pill"
              onClick={() => {
                router.push(ROUTES.PATIENT_PAGE.ADD_PATIENT);
              }}
            >
              Ajouter
            </button>
          </Space>
        }
      />
    </div>
  );
};
