import {
  FaNotesMedical,
  FaUserInjured,
  FaUserNurse,
  FaMailBulk,
  FaSpeakerDeck,
  FaBookDead,
} from "react-icons/fa";
import { ROUTES } from "../../../../routes";
import {
  GREEN_DARK,
  BS_WARNING,
  BS_BLUE,
  ORANGE_DARK,
} from "../../../../shared/colors";
import { AdminContainer } from "../../components/AdminContainer";
import { DossierListComponent } from "../../components/dossier/DossierList";
import { Space, Badge } from "antd";
import { MiniCard } from "../../../shared/CustomCard";

export const RegistreDecesPage = () => {
  return (
    <AdminContainer clicked="registre-deces">
      <div className="row mb-0 pb-0 align-items-center justify-content-center">
        <h2 className="col-md-4 mt-auto"> Registre des deces </h2>
        <div className="col-md-6 ms-auto">
          <Space>
            {DecesCardsDashboard.map((card) => (
              <MiniCard card={card} />
            ))}
          </Space>
        </div>
        <hr className="mb-2" />
      </div>

      <DossierListComponent
        deces={true}
        key={"list-deces"}
        title="Registre des deces"
      />
    </AdminContainer>
  );
};

const DecesCardsDashboard = [
  {
    title: "Patients",
    description: "Patients",
    value: 145,
    color: BS_WARNING,
    icon: <FaUserInjured size={48} color={BS_WARNING} />,
    link: ROUTES.PATIENT_PAGE.LIST_PATIENT,
  },
  {
    title: "Medecins",
    description: "Medecins",
    value: 25,
    color: BS_BLUE,
    icon: <FaUserNurse size={48} color={BS_BLUE} />,
    link: "/admin/users/list",
  },
  {
    title: "Deces",
    description: " Deces ",
    value: "1,7 M",
    color: ORANGE_DARK,
    icon: <FaBookDead size={48} color="red" />,
    link: "/admin/analyse/demandes/list",
  },
];
