import { Space, Tooltip } from "antd";
import { FaInfoCircle, FaPencilRuler } from "react-icons/fa";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../routes";
import { GREEN_DARK } from "../shared/colors";
import { modePCTag, dossierStatutTag } from "../modules/shared/Layouts";
import { DossierEntity } from "./dossier.entity";
import { MedecinEntity } from "./medecin.entity";
import { FormationSanitaireEntity } from "./formationSanitaire";

export class PatientEntity {
  _id: string;
  code!: string;
  niu!: string;
  nom: string;
  prenom: string;
  sexe!: boolean;
  date_naissance!: string;
  adresse_mail!: string;
  adresse!: string;
  telephone!: string;
  nationalite!: string;
  religion!: string;
  profession!: string;
  nom_urgence!: string;
  telephone_urgence!: string;
  _identite!: string;
  enregistrer_par!: number;
  statut!: number;
  dossiers!: DossierEntity[];
  mode!: number; // mode de prise en charge
  dossier_id!: number;
  nb_dossiers!: number;
  praticien!: MedecinEntity;
  formation_sanitaire!: FormationSanitaireEntity;
  disabled: number;

  constructor(patient: PatientEntity) {
    this._id = patient._id;
    this.code = patient.code;
    this.prenom = patient.prenom;
    this.nom = patient.nom;
    this.disabled = patient.disabled;
  }
}

export const PATIENT_COLUMNS = () => {
  console.log();
  const router = useHistory();
  return [
    {
      title: "Nom",
      key: "patient-nom",
      render: (cell: any, row: any) => {
        return (
          <div>
            <span className="text-uppercase">{cell?.nom + " "}</span>
            {cell?.prenom}{" "}
          </div>
        );
      },
    },
    {
      title: "Date naissance",
      key: "patient-date_naissance",
      render: (cell: any, row: any) => {
        return <span>{cell.date_naissance}</span>;
        // return <span>17/07/96</span>;
      },
    },
    {
      title: "Formation sanitaire",
      key: "patient-formation_sanitaire",
      render: (cell: any, row: any) => {
        return <span>{row?.formation_sanitaire?.designation} </span>;
        // return <span>Polyclinique de Tsinga </span>;
      },
    },
    {
      title: "N Dossier",
      key: "patient-last-dossier",
      render: (cell: any, row: any) => {
        return row.formation_sanitaire ? (
          <span>
            {row?.formation_sanitaire?.id +
              "Y" +
              row?.id +
              "P" +
              row?.dossier_id}{" "}
          </span>
        ) : (
          <span></span>
        );
      },
    },
    {
      title: "Praticien",
      key: "patient-praticien",
      render: (cell: any, row: any) => {
        return row.praticien ? (
          <span>{row?.praticien?.nom + " " + row?.praticien?.prenom} </span>
        ) : (
          ""
        );
        // return <span>Lekina Florent </span>;
      },
    },
    {
      title: "Prise en charge",
      key: "patient-mode",
      render: (cell: any, row: any) => {
        return <div className="d-flex mx-auto"> {modePCTag(row.mode)} </div>;
      },
    },
    {
      title: "Statut",
      key: "patient-etape",
      render: (cell: any, row: any) => {
        return (
          <div className="d-flex mx-auto"> {dossierStatutTag(row.statut)} </div>
        );
      },
    },
    {
      title: "Actions",
      key: "patient-patient-actions",
      render: (cell: any, row: any) => {
        return (
          <Space>
            {/* consulter la fiche du patient */}
            <Tooltip title="Consulter la fiche du patient">
              <FaInfoCircle
                size={24}
                color="#FF4D4F"
                style={{ cursor: "pointer" }}
                onClick={() => {
                  router.push({
                    pathname: ROUTES.PATIENT_PAGE.SHOW_PATIENT,
                    state: {
                      patient: row,
                      readOnly: true,
                    },
                  });
                }}
              />
            </Tooltip>
            <Tooltip title="Traiter le patient">
              <FaPencilRuler
                size={24}
                color={GREEN_DARK}
                style={{ cursor: "pointer" }}
                onClick={() => {
                  router.push({
                    pathname: ROUTES.REGISTRE_PAGE.PRINCIPAL,
                    state: {
                      patient: row?.id,
                      dossier: row?.dossier_id,
                    },
                  });
                }}
              />
            </Tooltip>
          </Space>
        );
      },
    },
  ];
};
