import {
  FaMailBulk,
  FaNotesMedical,
  FaUserInjured,
  FaUserNurse,
} from "react-icons/fa";
import { AdminContainer } from "../components/AdminContainer";
import {
  BS_BLUE,
  BS_WARNING,
  GREEN_DARK,
  ORANGE_DARK,
} from "../../../shared/colors";
import { ROUTES } from "../../../routes";
import { CardDashboard } from "../../shared/CustomCard";

export const AdminDashboard = () => {
  return (
    <AdminContainer clicked="admin-dashboard">
      <h2 className="mt-3" style={{ fontFamily: "revert-layer" }}>
        {" "}
        Welcome ADMIN !
      </h2>
      <div className="row d-flex ms-auto" style={{ marginTop: "10 %" }}>
        {AdminCardsDashboard.map((card) => (
          <CardDashboard card={card} />
        ))}
      </div>
    </AdminContainer>
  );
};

const AdminCardsDashboard = [
  {
    title: "Dossiers",
    description: "Dossiers",
    value: 14700,
    color: GREEN_DARK,
    icon: () => <FaNotesMedical size={48} color={GREEN_DARK} />,
    link: "/admin/registre/all",
  },
  {
    title: "Patients",
    description: "Patients",
    value: 145,
    color: BS_WARNING,
    icon: () => <FaUserInjured size={48} color={BS_WARNING} />,
    link: ROUTES.PATIENT_PAGE.LIST_PATIENT,
  },
  {
    title: "Medecins",
    description: "Medecins",
    value: 25,
    color: BS_BLUE,
    icon: () => <FaUserNurse size={48} color={BS_BLUE} />,
    link: "/admin/users/list",
  },
  {
    title: "Demandes",
    description: "Demandes",
    value: 17,
    color: ORANGE_DARK,
    icon: () => <FaMailBulk size={48} color={ORANGE_DARK} />,
    link: "/admin/analyse/demandes/list",
  },
];
