import { useEffect, useState } from "react";
import { fetchMedecins } from "../../network";
import { Space } from "antd";
import { DataTable } from "../../../shared/Table";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../../routes";
import {
  MEDECIN_COLUMNS,
  MedecinEntity,
} from "../../../../entities/medecin.entity";
import { PATIENT_COLUMNS } from "../../../../entities/patient.entity";

export const ListUserComponent = () => {
  const router = useHistory();
  const [isLoading, setIsLoading] = useState(true);
  const [medecins, setMedecins] = useState<MedecinEntity[]>([]);

  useEffect(() => {
    fetchMedecins().then((data) => {
      if (data.success) {
        console.log("liste des medecins : ", data.result);
        setMedecins(data.result);
        setIsLoading(false);
      }
    });
  }, []);

  // console.log("why medecins columns doesn't work ", MEDECIN_COLUMNS);

  return (
    <div>
      <DataTable
        key={"list-user-table"}
        loading={isLoading}
        data={medecins}
        columns={MEDECIN_COLUMNS()}
        filterFunction={(medecins: MedecinEntity, filterValue: string) =>
          medecins.nom.toLowerCase().includes(filterValue) ||
          medecins.prenom.toLowerCase().includes(filterValue)
        }
        buttons={
          <Space>
            <button
              className="btn btn-outline-primary"
              onClick={() => {
                router.push(ROUTES.USER_PAGE.ADD_USER);
              }}
            >
              Ajouter
            </button>
          </Space>
        }
      />
    </div>
  );
};
