import { Tabs, TabsProps } from "antd";
import { BsPersonBoundingBox } from "react-icons/bs";
import { FaBookOpen, FaFileMedical, FaHistory } from "react-icons/fa";
import { PatientInfoForm } from "./PatientInfoForm";
import { PatientAntecedentForm } from "./PatientAntecedentForm";
import { PatientActiviteForm } from "./PatientActiviteForm";
import { useState } from "react";

// selectionner le patient ou creer un nouveau patient
export const PatientForm = ({
  patient,
  readOnly,
}: {
  patient?: any;
  readOnly?: boolean;
}) => {
  const [activeTab, setActiveTab] = useState("1");

  const items: TabsProps["items"] = [
    {
      key: "1",
      label: (
        <span>
          <BsPersonBoundingBox className="mx-3" size={28} />
          Identification
        </span>
      ),
      children: <PatientInfoForm patient={patient} readOnly={readOnly} />,
    },
    // {
    //   key: "2",
    //   label: (
    //     <span>
    //       <FaFileMedical className="mx-3" size={28} />
    //       Antcedents
    //     </span>
    //   ),
    //   children: <PatientAntecedentForm />,
    // },
    // {
    //   key: "3",
    //   label: (
    //     <span>
    //       <FaBookOpen className="mx-3" size={28} />
    //       Dossiers mdicaux
    //     </span>
    //   ),
    //   children: <PatientActiviteForm />,
    // },
  ];

  return (
    <div>
      <Tabs
        defaultActiveKey="1"
        items={items}
        activeKey={activeTab}
        onTabClick={(key) => setActiveTab(key)}
      />
    </div>
  );
};
