import {
  DatePicker,
  Form,
  Select,
  SelectProps,
  Space,
  Spin,
  notification,
} from "antd";
import { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import {
  createDossier,
  fetchDossier,
  fetchOrganisme,
  fetchPatients,
  updateDossier,
} from "../../network";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../shared/rubrique";
import { OrganismeEntity } from "../../../../entities/organisme.entity";
import { ROUTES } from "../../../../routes";
import { formRules } from "../../../../shared/formRules";
import { InputLayout } from "../../../shared/Layouts";

// selectionner le patient ou creer un nouveau patient
export const DossierAjout = ({
  closeModal,
  dossier,
  formationSanitaire,
}: {
  closeModal?: () => void;
  dossier?: any;
  formationSanitaire?: any;
}) => {
  const router = useHistory();
  const [form] = Form.useForm();
  const [isLoading, setIsLoading] = useState(false);
  const [patients, setPatients] = useState<any>([]);
  const [formationSanitaires, setFormationSanitaires] = useState<
    OrganismeEntity[]
  >([]);
  const [reloadData, setReloadData] = useState(true);
  const [isLoadingData, setIsLoadingData] = useState(true);
  const [selectedPatient, setSelectedPatient] = useState<any>();
  const [selectedFormationSanitaire, setSelectedFormationSanitaire] =
    useState<OrganismeEntity>();
  const optionsPT: SelectProps["options"] = [];
  const optionsFS: SelectProps["options"] = [];

  console.log("patient selectionn dans DossierAjout: ", dossier);

  useEffect(() => {
    form.resetFields();
    if (reloadData) {
      fetchPatients().then((data: any) => {
        if (data.success) {
          setPatients(data.result);
          setReloadData(false);
        }
      });
    }
    fetchOrganisme().then((data) => {
      if (data.success) {
        setFormationSanitaires(
          data.result.filter(
            (elt) => elt.type_organisme === CODE_RUBRIQUES.FORMATION_SANITAIRE
          )
        );
        setIsLoadingData(false);
      }
    });

    if (dossier) form.setFieldValue("patient", dossier.patient.id);
  }, [dossier, form, reloadData]);

  console.log("ajout dossier pour patient : ", patients);

  const handlePatientChange = (patientId: any) => {
    setSelectedPatient(
      patients.find((patient: any) => patient.id === patientId)
    );
  };

  const handleFSChange = (organismeId: any) => {
    setSelectedFormationSanitaire(
      formationSanitaires.find((organisme) => organisme.id === organismeId)
    );
  };

  patients.map((i: any) => {
    optionsPT.push({
      value: i.id,
      label: i.nom + "  " + i.prenom,
    });
  });

  formationSanitaires.map((i) => {
    optionsFS.push({
      value: i.id,
      label: i.designation,
    });
  });

  const SelectPatient = (
    <Select
      showSearch
      placeholder=""
      optionFilterProp="children"
      defaultActiveFirstOption
      filterOption={(input: any, option: any) =>
        (option?.label ?? "").toLowerCase().includes(input)
      }
      filterSort={(optionA: any, optionB: any) =>
        (optionA?.label ?? "")
          .toLowerCase()
          .localeCompare((optionB?.label ?? "").toLowerCase())
      }
      options={optionsPT}
      className="col-12"
      onChange={handlePatientChange}
      disabled={dossier ? true : false}
    />
  );

  const SelectFormationSanitaire = (
    <Select
      showSearch
      placeholder=""
      optionFilterProp="children"
      defaultActiveFirstOption
      filterOption={(input: any, option: any) =>
        (option?.label ?? "").toLowerCase().includes(input)
      }
      filterSort={(optionA: any, optionB: any) =>
        (optionA?.label ?? "")
          .toLowerCase()
          .localeCompare((optionB?.label ?? "").toLowerCase())
      }
      options={optionsFS}
      className="col-12"
      onChange={handleFSChange}
    />
  );

  return (
    <Spin spinning={isLoading}>
      <div className="row">
        <div className="col-md-5 ms-auto mb-3">
          {/* <button
          type="submit"
          className="btn btn-outline-primary rounded-pill"
          name="init-dossier-btn-submit"
          onClick={() => router.push(ROUTES.PATIENT_PAGE.ADD_PATIENT)}
        >
          Ajouter un patient
        </button> */}
        </div>
        <Form
          key={"init-dossier-form"}
          form={form}
          name="init-dossier-form"
          labelCol={{ span: 8 }}
          className="row mt-3"
          onFinish={async (data) => {
            if (!dossier) {
              setIsLoading(true);
              let dossierToCreate = {
                patient: selectedPatient?.id,
                dateEntree: data?.dateEntree.toLocaleString(),
                dateSortie: null, // le dossier n'a pas encore t ferme
                statut: DOSSIER_STATUT.motif, // ouvert
                visibilite: true, // dossier non top secret
                transfert: false,
                transfertNb: 0,
                puffer: false,
                pufferNb: 0,
                formation_sanitaire: selectedFormationSanitaire?.id,
                disabled: 0,
                lastUpdate: new Date().toLocaleString(),
                praticien: "04545", // identifiant du praticien connect
              };
              console.log("dossier  creer ", dossierToCreate, selectedPatient);
              await createDossier(dossierToCreate)
                .then(async (data) => {
                  if (data.success) {
                    notification.success({
                      message: "Succs",
                      description: data.message,
                    });
                    await fetchDossier(data.result as any).then(
                      (newDossierData: any) => {
                        setIsLoading(false);
                        console.log("get new dossier ", newDossierData);
                        if (newDossierData.success) {
                          router.push({
                            pathname: ROUTES.REGISTRE_PAGE.ADD_DOSSIER,
                            state: {
                              dossier: newDossierData.result, // contient l'identifiant de l'element qui vient d'etre cr
                              readOnly: false,
                            },
                          });
                        }
                      }
                    );
                  } else {
                    setIsLoading(false);
                    notification.error({
                      message: "Erreur",
                      description: data.message,
                    });
                  }
                })
                .catch((error) => {
                  setIsLoading(false);
                  notification.error({
                    message: "Erreur",
                    description: error.message,
                  });
                });
            } else {
              // medecin traitant
              setIsLoading(true);
              console.log(
                "mettre  jour les informations du medecin traitant pour le dossier selectionn"
              );
              let dossierToUpdate = {
                step: "2",
                statut: DOSSIER_STATUT.consultation1, // premiere tape du traitement
                praticien: "0007", //--> medecinTraitant : id du medecin connect
                // fs_traitement: selectedFormationSanitaire?.id, // fs selectionne par le medecin connect
                // date_deb_traitement: data?.date_deb_traitement.toLocaleString(),
                lastUpdate: new Date().toLocaleString(),
              };
              await updateDossier(dossier.id, dossierToUpdate).then((data) => {
                if (data.success) {
                  setIsLoading(false);
                  notification.success({
                    message: "Succes",
                    description: "Dossier pass en phase de traitement.",
                  });
                  router.push(ROUTES.REGISTRE_PAGE.PRINCIPAL);
                } else {
                  setIsLoading(false);
                  notification.error({
                    message: "Erreur",
                    description: data.message,
                  });
                }
              });
            }
          }}
        >
          {dossier ? (
            <InputLayout
              label="Medecin traitant"
              key={"get-medecin-form"}
              name={"medecin_traitant"}
              rules={[formRules.ruleNoRequired]}
              disabled={true}
            />
          ) : (
            <InputLayout
              label="Medecin"
              key={"get-medecin-form"}
              name={"medecin_anamnese"}
              rules={[formRules.ruleNoRequired]}
              disabled={true}
            />
          )}
          <Form.Item
            label="Patient"
            key={"get-patient-form"}
            name={"patient"}
            rules={[
              {
                required: true,
                message: "Veuillez remplir ce champ",
              },
            ]}
          >
            {SelectPatient}
          </Form.Item>
          <Form.Item
            key={"get-fs-form"}
            label="Formation sanitaire"
            name={dossier ? "fs_traitant" : "formation_sanitaire"}
            rules={[
              {
                required: true,
                message: "Veuillez completer ce champ",
              },
            ]}
          >
            {SelectFormationSanitaire}
          </Form.Item>
          <Form.Item
            key={"get-date-form"}
            label="Recu le "
            name={"date_deb_traitement"}
            rules={[
              {
                required: true,
                message: "Veuillez completer ce champ",
              },
            ]}
          >
            <DatePicker />
          </Form.Item>
          <Space className="ms-auto col-md-8 mt-5">
            <button
              name="init-dossier-btn-cancel"
              type="button"
              className="btn btn-outline-secondary rounded-pill"
              onClick={() => {
                closeModal?.();
              }}
            >
              Annuler
            </button>
            <button
              type="submit"
              className="btn btn-primary rounded-pill"
              name="init-dossier-btn-submit"
            >
              {dossier ? "Traiter le dossier" : "Crer le dossier"}
            </button>
          </Space>
        </Form>
      </div>
    </Spin>
  );
};
