import { useHistory } from "react-router-dom";
import { AdminContainer } from "../../components/AdminContainer";
import { DossierListComponent } from "../../components/dossier/DossierList";

export const RegistrePraticienPage = () => {
  const router = useHistory();
  const state = router.location.state ? (router.location.state as any) : null;

  return (
    <AdminContainer clicked="registre-praticien" praticien={state?.praticien}>
      <h2> Registre | Praticien </h2> <hr />
      <DossierListComponent
        key={"list-praticient"}
        praticien={state?.praticien ? state?.praticien : "0007"}
        title={" Registre | Praticien "}
      />
    </AdminContainer>
  );
};
