import { Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  RubriqueNavigate,
  ScoreLayout,
  ScoreFormValue,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";

import { RubriqueEntity } from "../../../../../entities/rubrique.entity";
import { completeDossier } from "../../../network";
import { getPayload } from "../DossierDetails";

export const FormScore = ({
  dossier,
  eltDossiers,
  rubriques,
  activeKey,
  setActiveKey,
  reloadData,
}: {
  dossier: any;
  eltDossiers: any;
  rubriques: RubriqueEntity[];
  activeKey: number;
  setActiveKey: (tag: number) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);
  const [selectedScore, setSelectedScore] = useState("");
  const [scoreGCS, setScoreGCS] = useState(
    getPayload(dossier, DOSSIER_STATUT.score, CODE_RUBRIQUES.SCORE_GCS, false)
      ? getPayload(
          dossier,
          DOSSIER_STATUT.score,
          CODE_RUBRIQUES.SCORE_GCS,
          false
        )
      : 0
  );
  const [scoreAIS, setScoreAIS] = useState(0);
  const [scoreISS, setScoreISS] = useState(0);
  const [scoreRTS, setScoreRTS] = useState(0);
  const [scoreTRISS, setScoreTRISS] = useState(0);
  const [scoreMESS, setScoreMESS] = useState(0);
  const [scoreGHOISS, setScoreGHOISS] = useState(0);

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-score-form"
        key="acq-score-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.score ? "collapse show" : "d-none"
        }
        id="acq-score"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          // let dataToSend: any = form.getFieldsValue();
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.decision,
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_GCS,
                valeur: [scoreGCS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_ISS,
                valeur: [scoreISS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_RTS,
                valeur: [scoreRTS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_TRISS,
                valeur: [scoreTRISS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_MESS,
                valeur: [scoreMESS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SCORE_GHOISS,
                valeur: [scoreGHOISS],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          console.log("data to post in score form : ", dataToSend);
          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              // set check icon to success (donnes enregistres)
              setActiveKey(DOSSIER_STATUT.decision);
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
              // set check icon to danger (non enregistr)
            }
          });
          setIsLoadingForm(false);
        }}
      >
        {/* ALIGNER LES SCORES DANS UN TAG DE NAVIGATION COMME LES RUBRIQUES */}
        <div className="card card-body">
          <RubriqueTitle
            key="dossier-acq-score"
            title="Score prdictifs - Trauma Score"
          />
          <div>
            <ScoreFormValue
              selectedScore={selectedScore}
              setSelectedScore={setSelectedScore}
              scores={{
                GCS: { value: scoreGCS, setValue: setScoreGCS },
                AIS: { value: scoreAIS, setValue: setScoreAIS },
                ISS: { value: scoreAIS, setValue: setScoreAIS },
                RTS: { value: scoreAIS, setValue: setScoreAIS },
                TRISS: { value: scoreAIS, setValue: setScoreAIS },
                MESS: { value: scoreAIS, setValue: setScoreAIS },
                GHOISS: { value: scoreAIS, setValue: setScoreAIS },
              }}
            />
            <hr />
            {dossier.statut > DOSSIER_STATUT.score ? (
              <span></span>
            ) : (
              <ScoreLayout
                selectedScore={selectedScore}
                scoreGCS={setScoreGCS}
                scoreISS={setScoreISS}
                scoreRTS={setScoreRTS}
                scoreTRISS={setScoreTRISS}
                scoreMESS={setScoreMESS}
                scoreGHOISS={setScoreGHOISS}
              />
            )}
          </div>
          {dossier.statut > DOSSIER_STATUT.score ? (
            <Space className="ms-auto mt-5">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.diagnostic)}
              >
                Precedent
              </button>
              <button
                type="button"
                className="btn btn-primary rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.decision)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-score" />
          )}
        </div>
      </Form>
    </Spin>
  );
};
