import { Descriptions, Tabs, TabsProps } from "antd";
import { FaUserNurse } from "react-icons/fa";
import { BsPersonBoundingBox } from "react-icons/bs";
import { EltRubriqueEntity } from "../../../../entities/eltRubrique.entity";
import { useEffect, useState } from "react";
import { OrganismeEntity } from "../../../../entities/organisme.entity";
import { fetchEltRubrique, fetchOrganisme } from "../../network";
import { CODE_RUBRIQUES } from "../../../../shared/rubrique";

export const InfoUserComponent = ({ user }: { user: any }) => {
  const [eltRubriques, setEltRubriques] = useState<EltRubriqueEntity[]>([]);
  const [organismes, setOrganismes] = useState<OrganismeEntity[]>([]);

  useEffect(() => {
    fetchEltRubrique()
      .then((data) => {
        if (data.success) {
          setEltRubriques(data.result);
        }
      })
      .then(() => {
        fetchOrganisme().then((data) => {
          if (data.success) {
            setOrganismes(data.result);
          }
        });
      });
  }, []);

  const items: TabsProps["items"] = [
    {
      key: "1",
      label: (
        <span>
          <BsPersonBoundingBox className="mx-3" size={28} />
          Donnes personnelles
        </span>
      ),
      children: (
        <div className="row mx-3">
          <Descriptions title="" column={1}>
            <Descriptions.Item label="Nom">{user.nom}</Descriptions.Item>
            <Descriptions.Item label="Prnom">{user.prenom}</Descriptions.Item>
            <Descriptions.Item label="Sexe">{user.sexe}</Descriptions.Item>
            <Descriptions.Item label="Date de naissance">
              {user.date_naissance}
            </Descriptions.Item>
            <Descriptions.Item label="Adresse mail">
              {user.adresse_mail}
            </Descriptions.Item>
            <Descriptions.Item label="Telephone">
              {user.telephone}
            </Descriptions.Item>
            <Descriptions.Item label="Adresse">
              {user.adresse}
            </Descriptions.Item>
          </Descriptions>
        </div>
      ),
    },
    {
      key: "2",
      label: (
        <span>
          <FaUserNurse className="mx-3" size={28} />
          Statut mdical
        </span>
      ),
      children: (
        <div className="row mx-3">
          <Descriptions title="" column={1}>
            <Descriptions.Item label="N ONMC">{user.id}</Descriptions.Item>
            <Descriptions.Item label="Qualification">
              {
                eltRubriques.find((elt) => elt.id === user.qualification)
                  ?.designation
              }
            </Descriptions.Item>
            <Descriptions.Item label="Annes d'experience">
              {user.date_fonction}
            </Descriptions.Item>
            <Descriptions.Item label="Rseaux de traumatologie">
              <ul>
                {organismes.map((org) => {
                  if (
                    user.organismes.includes(org.id) &&
                    org.type_organisme === CODE_RUBRIQUES.RESEAU_TRAUMATOLOGIE
                  ) {
                    return <li>{org.designation}</li>;
                  }
                })}
              </ul>
            </Descriptions.Item>
            <Descriptions.Item label="Formations sanitaires">
              <ul>
                {organismes.map((org) => {
                  if (
                    user.organismes.includes(org.id) &&
                    org.type_organisme === CODE_RUBRIQUES.FORMATION_SANITAIRE
                  ) {
                    return <li>{org.designation}</li>;
                  }
                })}
              </ul>
            </Descriptions.Item>
          </Descriptions>
        </div>
      ),
    },
  ];

  return (
    <div>
      <Tabs defaultActiveKey="1" items={items} />
    </div>
  );
};
