import { Space } from "antd";
import { BS_COLOR } from "../../../../../shared/colors";
import { RUBRIQUES_TAGS_AMN } from "../../../../../shared/rubrique";
import { FormDiagnostic } from "../rubriques/FormDiagnostic";
import { FormEnquete } from "../rubriques/FormEnquete";
import { FormExamen } from "../rubriques/FormExamen";
import { FormHistoire } from "../rubriques/FormHistoire";
import { FormMotif } from "../rubriques/FormMotif";
import { FormScore } from "../rubriques/FormScore";
import { FormStatut } from "../rubriques/FormStatut";
import { EltDossierEntity } from "../../../../../entities/eltDossier.entity";
import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { RubriqueEntity } from "../../../../../entities/rubrique.entity";
import { RubriqueTag } from "../../../../shared/Layouts";
import { useState } from "react";
import { FormAntecedent } from "../rubriques/FormAntecedent";
import { FormComorbidite } from "../rubriques/FormComorbidite";

export const DossierAnamnese = ({
  dossier,
  eltDossiers,
  eltRubriques,
  rubriques,
  setActiveTag,
  reloadData,
}: {
  dossier: any;
  eltDossiers: EltDossierEntity[];
  eltRubriques: EltRubriqueEntity[];
  rubriques: RubriqueEntity[];
  setActiveTag: (tag: string) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [activeKey, setActiveKey] = useState(dossier.statut);
  // Sonde moi oh Dieu et connais mon coeur, eprouve moi et connais mes penses.
  // Regarde si je suis sur une mauvaise voie et garde moi sur la voie de l'eternite
  // Ne plus reflechir, juste me laisser conduire

  return (
    <div>
      <div id="acq-tag">
        <Space className="mb-3 btn-group" wrap>
          {RUBRIQUES_TAGS_AMN.map((tag) => (
            <RubriqueTag
              name={tag.value}
              label={tag.label}
              bscolor={tag.bscolor as BS_COLOR}
              activeKey={activeKey}
              setActiveKey={setActiveKey}
              statut={dossier.statut}
            />
          ))}
        </Space>
      </div>
      {/* MOTIF DE CONSULTATION */}
      <FormMotif
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* HISTOIRE DE LA MALADIE */}
      <FormHistoire
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      <FormAntecedent
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      <FormComorbidite
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* ENQUETES DES SYSTEMES DE RECHERCHE */}
      <FormEnquete
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* EXAMEN PHYSIQUE */}
      <FormExamen
        dossier={dossier}
        reloadData={reloadData}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* DIAGNOSTIC DES LESIONS */}
      <FormDiagnostic
        dossier={dossier}
        reloadData={reloadData}
        eltDossiers={eltDossiers}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* TRAUMA SCORE */}
      <FormScore
        dossier={dossier}
        reloadData={reloadData}
        eltDossiers={eltDossiers}
        rubriques={rubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
      />
      {/* STATUS CLINIQUE */}
      <FormStatut
        dossier={dossier}
        eltRubriques={eltRubriques}
        activeKey={activeKey}
        setActiveKey={setActiveKey}
        setActiveTag={setActiveTag}
        reloadData={reloadData}
      />
    </div>
  );
};
