import { Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  RubriqueNavigate,
  RadioLayout,
  FullSelectLayout,
} from "../../../../shared/Layouts";
import { useState } from "react";
import {
  CODE_RUBRIQUES,
  DOSSIER_DECISION,
  DOSSIER_STATUT,
} from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";
import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";
import { FormDeces } from "./FormDeces";
import { FormTransfert } from "./FormTransfert";
import { FormTampon } from "./FormTampon";

export const FormStatut = ({
  dossier,
  eltRubriques,
  activeKey,
  setActiveKey,
  setActiveTag,
  reloadData,
}: {
  dossier: any;
  eltRubriques: EltRubriqueEntity[];
  activeKey: number;
  setActiveKey: (key: number) => void;
  setActiveTag: (tag: string) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [selectedFormStatut] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);
  const [selectedStatut, setSelectedStatut] = useState("");

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-statut-form"
        key="acq-statut-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.decision ? "collapse show" : "d-none"
        }
        id="acq-statut"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);

          let dataToSend = {
            dossierId: dossier.id,
            statut:
              selectedStatut === DOSSIER_DECISION.deces
                ? DOSSIER_STATUT.decede
                : DOSSIER_STATUT.anamnese,
            step: selectedStatut === DOSSIER_DECISION.deces ? "1" : "2",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.STATUT_PRETHERA,
                valeur: [data.statut_prethera],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.STATUT_DECISION,
                valeur: [data.statut_decision],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
            ...selectedFormStatut.getFieldsValue(),
            deces: selectedStatut === DOSSIER_DECISION.deces ? 1 : 0,
            transfert: selectedStatut === DOSSIER_DECISION.transfert ? 1 : 0,
            puffer: selectedStatut === DOSSIER_DECISION.tampon ? 1 : 0,
          };

          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              setActiveTag("2");
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
              // set check icon to danger (non enregistr)
            }
          });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle key="dossier-acq-statut" title="Statut clinique" />
          <div>
            <RadioLayout
              label="Statut clinique prthrapeutique"
              name="statut_prethera"
              key="statut_prethera"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.STATUT_PRETHERA
              )}
              rules={[formRules.ruleNoRequired]}
              defaultValue={getPayload(
                dossier,
                DOSSIER_STATUT.decision,
                CODE_RUBRIQUES.STATUT_PRETHERA,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.decision ? true : false}
            />
            <FullSelectLayout
              label="Decision thrapeutique"
              name="statut_decision"
              key="statut_decision"
              size={12}
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.STATUT_DECISION
              )}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.decision,
                CODE_RUBRIQUES.STATUT_DECISION,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.decision ? true : false}
              handleSelect={setSelectedStatut}
            />
            {selectedStatut === DOSSIER_DECISION.tampon ? (
              <FormTampon
                key={"statut-tampon-form"}
                formRef={selectedFormStatut}
              />
            ) : selectedStatut === DOSSIER_DECISION.transfert ? (
              <FormTransfert
                key={"statut-transfert-form"}
                formRef={selectedFormStatut}
              />
            ) : selectedStatut === DOSSIER_DECISION.deces ? (
              <FormDeces
                key={"statut-deces-form"}
                formRef={selectedFormStatut}
              />
            ) : (
              ""
            )}
          </div>
          {dossier.statut > DOSSIER_STATUT.decision ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill"
                onClick={() => setActiveKey(DOSSIER_STATUT.score)}
              >
                Precedent
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-statut" />
          )}
        </div>
      </Form>
    </Spin>
  );
};
