import styled from "@emotion/styled";
import { ReactNode } from "react";
import { PRIMARY } from "../../../shared/colors";
import { useHistory } from "react-router-dom";
import { BiChevronDown } from "react-icons/bi";

const SidebarContainer = styled.div`
  .clicked {
    color: ${PRIMARY};
  }
`;

export const Sidebar = ({
  routes,
  clicked,
}: {
  routes: ItemBarType[];
  clicked: string;
}) => {
  const router = useHistory();

  return (
    <SidebarContainer>
      <aside id="sidebar" className="sidebar">
        <ul className="sidebar-nav" id="sidebar-nav">
          {routes.map((item) =>
            item.subItems ? (
              <li className="nav-item" key={item.clicked}>
                <div
                  className={
                    clicked.includes(item.clicked)
                      ? "nav-link active"
                      : "nav-link collapsed"
                  }
                  data-bs-target={`#${item.clicked}`}
                  data-bs-toggle="collapse"
                >
                  <i>{item.icon(clicked)}</i>
                  <span>{item.title}</span>{" "}
                  <i className="bi bi-chevron-down ms-auto">
                    <BiChevronDown />
                  </i>
                </div>
                <ul
                  id={`${item.clicked}`}
                  className={
                    clicked.includes(item.clicked)
                      ? "nav-content collapse show"
                      : "nav-content collapse"
                  }
                  data-bs-parent="#sidebar-nav"
                >
                  {item.subItems.map((subItem) => (
                    <li key={subItem.clicked}>
                      <div
                        className={clicked === subItem.clicked ? "active" : ""}
                        onClick={() =>
                          router.push({
                            pathname: subItem.link,
                            state: {
                              formationSanitaire: subItem.values
                                ? subItem.select
                                : null,
                            },
                          })
                        }
                        data-bs-toggle="collapse"
                        data-bs-target={"#collapseWidth-" + subItem.clicked}
                        aria-expanded="false"
                        aria-controls={"#collapseWidth-" + subItem.clicked}
                      >
                        <i>{subItem.icon(clicked)}</i>
                        <span>{subItem.title}</span>
                      </div>
                      {subItem.values && clicked === subItem.clicked ? (
                        <div
                          className="collapse"
                          id={"collapseWidth-" + subItem.clicked}
                        >
                          {subItem.values}
                        </div>
                      ) : (
                        ""
                      )}
                    </li>
                  ))}
                </ul>
              </li>
            ) : (
              <li className="nav-item" key={item.clicked}>
                <div
                  className={
                    clicked === item.clicked ? "nav-link active" : "nav-link"
                  }
                  onClick={() => router.push(item.link)}
                >
                  <i>{item.icon(clicked)}</i>
                  <span>{item.title}</span>
                </div>
              </li>
            )
          )}
        </ul>
      </aside>
    </SidebarContainer>
  );
};

type ItemBarType = {
  title: string;
  clicked: string;
  link: string;
  icon: (shrink: string) => ReactNode;
  subItems?: ItemBarType[];
  values?: React.ReactNode;
  select?: any;
};
