import { PDFViewer } from "@react-pdf/renderer";
import { PDFDossierComplet } from "../../../../services/pdfrenderer/PDFDossierComplet";
import { AdminContainer } from "../../components/AdminContainer";
import { Layout, notification } from "antd";
import { ROUTES } from "../../../../routes";
import { useEffect } from "react";
import { Link, useHistory } from "react-router-dom";

export const ShowDossierPage = () => {
  const router = useHistory();
  const { dossier, readOnly } = router.location.state
    ? (router.location.state as any)
    : {
        dossier: null,
        readOnly: false,
      };

  useEffect(() => {
    if (!dossier) {
      notification.error({
        message: "Erreur",
        description: "Veuillez selectionner un dossier pour le consulter",
      });
      router.push(ROUTES.REGISTRE_PAGE.PRINCIPAL);
    }
  }, [dossier, router]);

  return (
    <AdminContainer clicked="registre-all">
      <h2 className="">
        <Link to={ROUTES.REGISTRE_PAGE.PRINCIPAL}>Registre principal</Link> |
        DOSSIER N
        <span className="text-primary">
          {dossier.formation_sanitaire.id +
            "Y" +
            dossier?.patient.id +
            "P" +
            dossier.id}
        </span>
      </h2>
      <Layout>
        {/* <Content> */}
        <PDFViewer
          // showToolbar
          style={{
            width: window.innerWidth / 1.45, //the pdf viewer will take up all of the width and height
            height: window.innerHeight,
          }}
        >
          <PDFDossierComplet dossier={dossier} />
        </PDFViewer>
        {/* </Content> */}
      </Layout>
    </AdminContainer>
  );
};
