import { Button, Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  RubriqueNavigate,
  InputLayout,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";
import { FaImage } from "react-icons/fa";
import { LIGHT_PRIMARY } from "../../../../../shared/colors";
import { ButtonWithModal } from "../../../../shared/ButtonWithModal";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";

export const FormDiagnostic = ({
  dossier,
  eltDossiers,
  activeKey,
  setActiveKey,
  reloadData,
}: {
  dossier: any;
  eltDossiers: any;
  activeKey: number;
  setActiveKey: (tag: number) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-diagnostic-form"
        key="acq-diagnostic-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.diagnostic ? "collapse show" : "d-none"
        }
        id="acq-diagnostic"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.score, // motif de la consultation <==> anamnese
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DIAGNOSTIC_CLASS_AO,
                valeur: [data.diag_class_ao],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DIAGNOSTIC_LESSION_1,
                valeur: [data.diag_lessions_ass1],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DIAGNOSTIC_LESSION_2,
                valeur: [data.diag_lessions_ass2],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DIAGNOSTIC_LESSION_3,
                valeur: [data.diag_lessions_ass1],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              setActiveKey(DOSSIER_STATUT.score);
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
              // set check icon to danger (non enregistr)
            }
          });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle
            key="dossier-acq-diagnostic"
            title="Diagnostic des lesions musculosquelettiques - ICD 10"
          />
          <div>
            <div className="row" id="classification-ao">
              <InputLayout
                label="Classification AO"
                multiple={true}
                name="diag_class_ao"
                key="diag_class_ao"
                className="col-md-8"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.diagnostic,
                  CODE_RUBRIQUES.DIAGNOSTIC_CLASS_AO,
                  false
                )}
                disabled={
                  dossier.statut > DOSSIER_STATUT.diagnostic ? true : false
                }
              />
              {/* Inserer un poster de la classification AO */}
              <div className="col-md-4">
                <ButtonWithModal
                  buttonText={
                    <div className="">
                      <small className="col">Insrer un poster</small>{" "}
                      <FaImage size={28} color={LIGHT_PRIMARY} />
                    </div>
                  }
                  buttonProps={{
                    danger: false,
                    className: "mx-auto",
                  }}
                  modalProps={{ title: "" }}
                >
                  {(closeModal) => (
                    <div>
                      <h3>Insrer AO COIAC</h3>
                      <div className="card"></div>
                      <Space>
                        <Button type="primary" onClick={() => closeModal()}>
                          Fermer
                        </Button>
                        <Button
                          danger
                          onClick={() => {
                            notification.success({
                              message: "Succs",
                              description: "Laisse l'enseignement pass",
                            });
                          }}
                        >
                          Oui
                        </Button>
                      </Space>
                    </div>
                  )}
                </ButtonWithModal>
              </div>
            </div>
            <InputLayout
              label="Lesions associes 1 - ICD 10"
              name="diag_lessions_ass1"
              key="diag_lessions_ass1"
              className="col-md-8"
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.diagnostic,
                CODE_RUBRIQUES.DIAGNOSTIC_LESSION_1,
                false
              )}
              disabled={
                dossier.statut > DOSSIER_STATUT.diagnostic ? true : false
              }
            />
            <InputLayout
              label="Lesions associes 2 - ICD 10"
              name="diag_lessions_ass2"
              key="diag_lessions_ass2"
              className="col-md-8"
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.diagnostic,
                CODE_RUBRIQUES.DIAGNOSTIC_LESSION_2,
                false
              )}
              disabled={
                dossier.statut > DOSSIER_STATUT.diagnostic ? true : false
              }
            />
            <InputLayout
              label="Lesions associes 3 - ICD 10"
              name="diag_lessions_ass3"
              key="diag_lessions_ass3"
              className="col-md-8"
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.diagnostic,
                CODE_RUBRIQUES.DIAGNOSTIC_LESSION_3,
                false
              )}
              disabled={
                dossier.statut > DOSSIER_STATUT.diagnostic ? true : false
              }
            />
          </div>
          {dossier.statut > DOSSIER_STATUT.motif ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill"
                onClick={() => setActiveKey(DOSSIER_STATUT.examen)}
              >
                Precedent
              </button>
              <button
                type="button"
                className="btn btn-primary ml-3 rounded-pill"
                onClick={() => setActiveKey(DOSSIER_STATUT.score)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-diagnostic" />
          )}
        </div>
      </Form>
    </Spin>
  );
};
