export const AdminContainer = ({
  clicked,
  children,
  praticien,
  formation_sanitaire,
}: {
  clicked: string;
  children: ReactNode;
  praticien?: string | number; // id du praticien
  formation_sanitaire?: string | number; // id de la formation sanitaire
}) => {
  const [shrink, setShrink] = useState(false);
  const router = useHistory();
  // getLocal values
  const localPraticiens = localStorage.getItem("praticiens");
  const praticiens = localPraticiens ? JSON.parse(localPraticiens) : [];
  const localFS = localStorage.getItem("formationSanitaires");
  const formationSanitaires = localFS ? JSON.parse(localFS) : [];

  const [selectedPraticien, setSelectedPraticien] = useState(praticien);
  const [selectedFormationSanitaire, setSelectedFormationSanitaire] =
    useState(formation_sanitaire);
  const optionsPT: SelectProps["options"] = [];
  const optionsFS: SelectProps["options"] = [];

  const handleFSChange = (organismeId: any) => {
    setSelectedFormationSanitaire(
      formationSanitaires.find((organisme: any) => organisme.id === organismeId)
        ?.id
    );
    router.push({
      pathname: ROUTES.REGISTRE_PAGE.FORMATION_SANITAIRE,
      state: {
        formation_sanitaire: formationSanitaires.find(
          (organisme: any) => organisme.id === organismeId
        )?.id,
      },
    });
  };

  const handlePraticienChange = (praticienId: any) => {
    setSelectedPraticien(
      praticiens.find((praticien: any) => praticien.id === praticienId).id
    );
    router.push({
      pathname: ROUTES.REGISTRE_PAGE.PRATICIEN,
      state: {
        praticien: praticiens.find(
          (praticien: any) => praticien.id === praticienId
        ).id,
      },
    });
  };

  praticiens.map((i: any) => {
    optionsPT.push({
      value: i.id,
      label: i.nom + "  " + i.prenom,
    });
  });

  formationSanitaires.map((i: any) => {
    optionsFS.push({
      value: i.id,
      label: i.designation,
    });
  });

  const SelectPraticien = () => {
    return {
      node: (
        <Select
          showSearch
          placeholder=""
          optionFilterProp="children"
          defaultActiveFirstOption
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={optionsPT}
          className="col-12"
          defaultValue={selectedPraticien}
          onChange={handlePraticienChange}
        />
      ),
      value: selectedPraticien,
    };
  };

  const SelectFormationSanitaire = () => {
    return {
      node: (
        <Select
          showSearch
          placeholder=""
          optionFilterProp="children"
          defaultActiveFirstOption
          filterOption={(input: any, option: any) =>
            (option?.label ?? "").toLowerCase().includes(input)
          }
          filterSort={(optionA: any, optionB: any) =>
            (optionA?.label ?? "")
              .toLowerCase()
              .localeCompare((optionB?.label ?? "").toLowerCase())
          }
          options={optionsFS}
          className="col-12"
          defaultValue={selectedFormationSanitaire}
          onChange={handleFSChange}
        />
      ),
      value: selectedFormationSanitaire,
    };
  };

  return (
    <div className={shrink ? "toggle-sidebar" : ""}>
      <Navbar
        homeLink={ROUTES.ADMIN_PAGE.DASHBOARD}
        shrink={shrink}
        setShrink={setShrink}
      />

      <Sidebar
        routes={ADMIN_SIDEBAR_ITEMS(
          SelectFormationSanitaire(),
          SelectPraticien()
        )}
        clicked={clicked}
      />

      <main id="main" className="main">
        {/* <PageBreadcrumb /> */}
        <div>{children}</div>
      </main>

      <AppFooter />
      <a
        href={ROUTES.ADMIN_PAGE.DASHBOARD}
        className="back-to-top d-flex align-items-center justify-content-center"
      >
        <i className="bi bi-arrow-up-short"></i>
      </a>
    </div>
  );
};

