import { Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  FullSelectLayout,
  RubriqueNavigate,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";

import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";

export const FormEnquete = ({
  dossier,
  eltRubriques,
  activeKey,
  setActiveKey,
  reloadData,
}: {
  dossier: any;
  eltRubriques: EltRubriqueEntity[];
  activeKey: number;
  setActiveKey: (tag: number) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-enquete-form"
        key="acq-enquete-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.enquete ? "collapse show" : "d-none"
        }
        id="acq-enquete"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.examen, // motif de la consultation <==> anamnese
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_GENERAL,
                valeur: [data.enq_general],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_LOCOMOTEUR,
                valeur: [data.enq_app_locomoteur],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_NERVEUX,
                valeur: [data.enq_sys_nerveux],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_RESPIRATOIRE,
                valeur: [data.enq_app_respiratoire],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_CARDIO,
                valeur: [data.enq_app_cardio],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_DIGESTIF,
                valeur: [data.enq_app_digestif],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.ENQUETE_UROGENITAL,
                valeur: [data.enq_app_gnital],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              setActiveKey(DOSSIER_STATUT.examen);
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
            }
          });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle
            key="dossier-acq-enquete"
            title="Enqute des systmes"
          />
          <div>
            <FullSelectLayout
              label="Gneral"
              name="enq_general"
              key="enq_general"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_GENERAL
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_GENERAL,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Appareil locomoteur"
              name="enq_app_locomoteur"
              key="enq_app_locomoteur"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_LOCOMOTEUR
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_LOCOMOTEUR,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Sytme nerveux central et priphrique"
              name="enq_sys_nerveux"
              key="enq_sys_nerveux"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_NERVEUX
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_NERVEUX,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Appareil respiratoire"
              name="enq_app_respiratoire"
              key="enq_app_respiratoire"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_RESPIRATOIRE
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_RESPIRATOIRE,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Appareil cardio-vasculaire"
              name="enq_app_cardio"
              key="enq_app_cardio"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_CARDIO
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_CARDIO,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Appareil digestif"
              name="enq_app_digestif"
              key="enq_app_digestif"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_DIGESTIF
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_DIGESTIF,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
            <FullSelectLayout
              label="Appareil uro-gnital"
              name="enq_app_gnital"
              key="enq_app_gnital"
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.ENQUETE_UROGENITAL
              )}
              size={12}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.enquete,
                CODE_RUBRIQUES.ENQUETE_UROGENITAL,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.enquete ? true : false}
            />
          </div>
          {dossier.statut > DOSSIER_STATUT.enquete ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.histoire)}
              >
                Precedent
              </button>
              <button
                type="button"
                className="btn btn-primary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.examen)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-enquete" />
          )}{" "}
        </div>
      </Form>
    </Spin>
  );
};
