import { Link, useHistory } from "react-router-dom";
import { AdminContainer } from "../../components/AdminContainer";
import { AddUserComponent } from "../../components/Utilisateur/AddUserComponent";
import { ROUTES } from "../../../../routes";

export const ShowUserPage = () => {
  const router = useHistory();
  const state = router.location.state ? (router.location.state as any) : null;

  return (
    <AdminContainer clicked="users-list">
      <h2>
        <Link to={ROUTES.USER_PAGE.LIST_USER}>Liste des utilisateurs</Link> |
        Utilisateur M00P80Y{" "}
        <span className="text-primary m-0">{state?.user.id}</span>
      </h2>
      {/* <hr /> */}
      <div className="card mt-0">
        <div className="card-body">
          <AddUserComponent user={state?.user} readOnly={state?.readOnly} />
        </div>
      </div>
    </AdminContainer>
  );
};
