import { Form, FormInstance } from "antd";
import { DateLayout, InputLayout } from "../../../../shared/Layouts";
import { FaSignOutAlt, FaTrashAlt } from "react-icons/fa";
import { BS_BLUE } from "../../../../../shared/colors";
import { getPayload } from "../DossierDetails";
import { DOSSIER_STATUT, CODE_RUBRIQUES } from "../../../../../shared/rubrique";

export const FormTampon = ({ formRef }: { formRef: FormInstance<any> }) => {
  return (
    <Form
      name="acq-tampon-form"
      key="acq-tampon-form"
      form={formRef}
      id="acq-tampon"
      labelCol={{ span: 8 }}
      scrollToFirstError
    >
      <div className="row">
        <i className="col-md-1">
          <FaSignOutAlt size={24} color={BS_BLUE} />
        </i>
        <strong className="col-md-8">
          <h5 style={{ fontFamily: "monospace" }} className="text-primary">
            Sortie contre avis medical
          </h5>
        </strong>
      </div>
      <hr />
      <div>
        <DateLayout
          label="Date de la sortie"
          name="tampon_date"
          key={"tampon_date"}
          formRef={formRef}
        />
        <InputLayout
          label="Motif"
          name="tampon_motif"
          size={12}
          key={"tampon_motif"}
        />
        <InputLayout
          label="Notes"
          name="tampon_notes"
          multiple={true}
          size={12}
          key={"tampon_notes"}
        />
      </div>
    </Form>
  );
};
