import { Button, ButtonProps, Modal } from "antd";
import { useState } from "react";

export const ButtonWithModal = ({
  width,
  buttonText,
  children,
  modalProps,
  buttonProps,
  ...props
}: {
  width?: number;
  buttonText: React.ReactNode;
  children: (closeModal: () => void) => React.ReactNode;
  modalProps: Parameters<typeof Modal>[0];
  buttonProps?: ButtonProps;
}) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const closeModal = () => setIsModalVisible(false);
  return (
    <span {...props}>
      <Button
        onClick={() => setIsModalVisible(true)}
        // type="ghost"
        style={{ display: "flex", alignItems: "center" }}
        {...buttonProps}
      >
        {buttonText}
      </Button>
      <Modal
        open={isModalVisible}
        onCancel={closeModal}
        footer={null}
        {...modalProps}
        width={width}
      >
        {children(closeModal)}
      </Modal>
    </span>
  );
};

export const ModalButton = ({
  modalTitle,
  buttonText,
  buttonColor,
  children,
  width,
}: {
  buttonText: string;
  buttonColor: string;
  modalTitle: string;
  children: (closeModal: () => void) => React.ReactNode;
  width?: number;
}) => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const closeModal = () => setIsModalVisible(false);
  return (
    <div>
      <button className={"btn rounded-pill btn-outline-" + buttonColor}>
        {buttonText}
      </button>
      <Modal
        open={isModalVisible}
        onCancel={closeModal}
        footer={null}
        centered
        className="text-center"
        title={modalTitle}
        width={width}
      >
        {children(closeModal)}
      </Modal>
    </div>
  );
};
