import { AdminContainer } from "../../components/AdminContainer";
import { DossierListComponent } from "../../components/dossier/DossierList";

export const RegistreTraitementPage = () => {
  return (
    <AdminContainer clicked="registre-traitement">
      <h2> Registre des instances de traitement </h2>
      <DossierListComponent
        key={"list-traitement"}
        attente={true}
        title="Registre des instances de traitement"
      />
    </AdminContainer>
  );
};
