import { Route } from "react-router-dom";
import { AdminDashboard } from "../pages/AdminDashboard";
import { ComingSoonPage } from "../../shared/ComingSoon";
import { AddUserPage } from "../pages/Utilisateurs/AddUserPage";
import { UserListPage } from "../pages/Utilisateurs/UserListPage";
import { UserDemandePage } from "../pages/Utilisateurs/UserDemandePage";
import { AddPatientPage } from "../pages/Patients/AddPatientPage";
import { PatientListPage } from "../pages/Patients/PatientListPage";
import { RegistrePrincipalPage } from "../pages/Registre/RegistrePrincipalPage";
import { RegistreTraitementPage } from "../pages/Registre/RegistreTraitementPage";
import { RegistreTamponPage } from "../pages/Registre/RegistreTamponPage";
import { RegistreDecesPage } from "../pages/Registre/RegistreDecesPage";
import { NouvelleAnalyse } from "../pages/Analyse/NouvelleAnalyse";
import { AnalyseDemandePage } from "../pages/Analyse/AnalyseDemandePage";
import { AnalyseListDemandePage } from "../pages/Analyse/AnalyseListDemande";
import { AnalyseHistoriquePage } from "../pages/Analyse/AnalyseHistoriquePage";
import { ParametresDonneesPage } from "../pages/Parametres/ParametresDonneesPage";
import { ParametresConfig } from "../pages/Parametres/ParametresConfig";
import { ShowPatientPage } from "../pages/Patients/ShowPatientPage";
import { AddDossierPage } from "../pages/Registre/AddDossierPage";
import { ShowDossierPage } from "../pages/Registre/ShowDossierPage";
import { RegistreFSPage } from "../pages/Registre/RegistreFSPage";
import { RegistrePraticienPage } from "../pages/Registre/RegistrePraticienPage";
import { ShowUserPage } from "../pages/Utilisateurs/ShowUserPage";

export const AdminRoutes = () => {
  return (
    <>
      <Route exact path="/admin/dashboard" component={AdminDashboard} />
      <Route exact path="/admin/users" component={ComingSoonPage} />
      <Route exact path="/admin/users/new" component={AddUserPage} />
      <Route exact path="/admin/users/show" component={ShowUserPage} />
      <Route exact path="/admin/users/list" component={UserListPage} />
      <Route exact path="/admin/users/demandes" component={UserDemandePage} />
      <Route exact path="/admin/patients" component={ComingSoonPage} />
      <Route exact path="/admin/patients/new" component={AddPatientPage} />
      <Route exact path="/admin/patients/show" component={ShowPatientPage} />
      <Route exact path="/admin/patients/list" component={PatientListPage} />
      <Route exact path="/admin/registre" component={ComingSoonPage} />
      <Route
        exact
        path="/admin/registre/all"
        component={RegistrePrincipalPage}
      />
      <Route
        exact
        path="/admin/registre/dossier/complete"
        component={AddDossierPage}
      />
      <Route
        exact
        path="/admin/registre/dossier/show"
        component={ShowDossierPage}
      />
      <Route exact path="/admin/registre/fs" component={RegistreFSPage} />
      <Route
        exact
        path="/admin/registre/praticien"
        component={RegistrePraticienPage}
      />
      <Route
        exact
        path="/admin/registre/traitement"
        component={RegistreTraitementPage}
      />
      <Route
        exact
        path="/admin/registre/tampon"
        component={RegistreTamponPage}
      />
      <Route exact path="/admin/registre/deces" component={RegistreDecesPage} />

      <Route exact path="/admin/analyse" component={ComingSoonPage} />
      <Route exact path="/admin/analyse/new" component={NouvelleAnalyse} />
      <Route
        exact
        path="/admin/analyse/demandes/new"
        component={AnalyseDemandePage}
      />
      <Route
        exact
        path="/admin/analyse/demandes/list"
        component={AnalyseListDemandePage}
      />
      <Route
        exact
        path="/admin/analyse/historique"
        component={AnalyseHistoriquePage}
      />
      <Route exact path="/admin/documents" component={ComingSoonPage} />
      <Route
        exact
        path="/admin/documents/certificat"
        component={ComingSoonPage}
      />
      <Route
        exact
        path="/admin/documents/expertise"
        component={ComingSoonPage}
      />
      <Route exact path="/admin/documents/dossier" component={ComingSoonPage} />
      <Route exact path="/admin/parametres" component={ComingSoonPage} />
      <Route
        exact
        path="/admin/parametres/donnees"
        component={ParametresDonneesPage}
      />
      <Route
        exact
        path="/admin/parametres/configuration"
        component={ParametresConfig}
      />
    </>
  );
};

export default AdminRoutes;
