import { Avatar, List, Space, Spin, Tag, notification } from "antd";
import { useState, useEffect } from "react";
import { FaArrowDown, FaCheck, FaClock, FaWindowClose } from "react-icons/fa";
import { COLOR_LIST } from "../../../../shared/colors";
import { InfoUserComponent } from "./InfoUserComponent";
import { accepterDemande, fetchDemandes, rejeterDemande } from "../../network";

const getColor = () =>
  COLOR_LIST[Math.floor(Math.random() * COLOR_LIST.length)];

export const DemandeComponent = () => {
  const [isLoadingData, setIsLoadingData] = useState(true);
  const [demandes, setDemandes] = useState<any[]>([]);
  const [isReplying, setIsReplying] = useState(false);
  const [reload, setReload] = useState(true);

  useEffect(() => {
    if (reload) {
      fetchDemandes().then((data) => {
        if (data.success) {
          setDemandes(data.result);
          setIsLoadingData(false);
          setReload(false);
        } else {
          notification.error({
            message: "Erreur",
            description: "Impossible de rcuperer la liste des demandes",
          });
        }
      });
    }
  }, [reload]);

  const rejectDemande = async (item: any, motif?: string) => {
    setIsReplying(true);
    await rejeterDemande({
      demandeID: item.id,
      medecin: item.personne,
      motif: motif ? motif : null,
    })
      .then((data) => {
        if (data.success) {
          setIsReplying(false);
          notification.success({
            message: "Succes",
            description:
              "La demande a bien t rejete. Un mail sera envoy au mdecin.",
          });
          //   window.location.reload();
          setReload(true);
        }
      })
      .catch((err) => {
        notification.error({
          message: "Erreur",
          description:
            "L'opration n'a pu tre effectue. Veuillez reessayer plus tard.",
        });
      });
  };

  const acceptDemande = async (item: any, motif?: string) => {
    setIsReplying(true);
    await accepterDemande({
      demandeID: item.id,
      medecin: item.personne,
      motif: motif ? motif : null,
    })
      .then((data) => {
        if (data.success) {
          setIsReplying(false);
          notification.success({
            message: "Succes",
            description:
              "La demande a bien t accepte. Un mail sera envoy au mdecin.",
          });
          //   window.location.reload();
          setReload(true);
        }
      })
      .catch((err) => {
        notification.error({
          message: "Erreur",
          description:
            "L'opration n'a pu tre effectue. Veuillez reessayer plus tard.",
        });
      });
  };

  const ItemModal = (item: any) => {
    return (
      <div>
        <Spin spinning={isReplying}>
          <div className="modal fade" id={"itemModal-" + item.id} tabIndex={-1}>
            <div className="modal-dialog modal-dialog-centered">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title">Demande d'adhsion</h5>
                  <button
                    type="button"
                    className="btn-close"
                    data-bs-dismiss="modal"
                    aria-label="Close"
                  ></button>
                </div>
                <div className="modal-body">
                  <InfoUserComponent
                    user={item.personne}
                    key={"infoUser-" + item.id}
                  />
                </div>
                <div className="modal-footer">
                  {item.statut === 0 || item.statut === 1 ? (
                    <Space>
                      <button
                        type="button"
                        className="btn btn-outline-danger rounded-pill"
                        data-bs-dismiss="modal"
                        onClick={() => rejectDemande(item)}
                      >
                        Refuser
                      </button>
                      <button
                        type="button"
                        className="btn btn-outline-primary rounded-pill"
                        data-bs-dismiss="modal"
                        onClick={() => acceptDemande(item)}
                      >
                        Accepter
                      </button>
                    </Space>
                  ) : (
                    <span></span>
                  )}
                </div>
              </div>
            </div>
          </div>
        </Spin>
      </div>
    );
  };

  return (
    <div className="card">
      <div className="card-body">
        <Spin spinning={isLoadingData}>
          <List
            className="demo-loadmore-demandes"
            loading={isLoadingData}
            itemLayout="horizontal"
            dataSource={demandes}
            renderItem={(item) => (
              <List.Item
                actions={
                  item.statut === 0 || item.statut === 1
                    ? [
                        // <Button
                        //   key="demandes-loadmore-edit"
                        //   className="rounded-pill"
                        //   type="link"
                        //   size="small"
                        // >
                        //   accepter
                        // </Button>,
                        // <Button
                        //   key="demandes-loadmore-more"
                        //   className="rounded-pill"
                        //   type="link"
                        //   danger
                        //   size="small"
                        // >
                        //   refuser
                        // </Button>,
                        <span> {item.date_envoie}</span>,
                      ]
                    : [<span> Envoy le {item.date_reponse}</span>]
                }
                extra={ItemModal(item)}
              >
                <List.Item.Meta
                  avatar={
                    <Avatar
                      style={{
                        backgroundColor: getColor(),
                        verticalAlign: "middle",
                      }}
                      size="large"
                    >
                      {item.personne.nom.charAt(0) +
                        item.personne.prenom.charAt(0)}
                    </Avatar>
                  }
                  title={
                    <div className="row">
                      <span className="col-md-4">
                        {item.personne.nom + "  " + item.personne.prenom}
                      </span>
                      <span className="col-md-8 ms-auto text-end">
                        {item.statut === 0 || item.statut === 1
                          ? DemandeTag("en attente")
                          : item.statut === 2
                          ? DemandeTag("accepte")
                          : DemandeTag("rejete")}
                      </span>
                    </div>
                  }
                  description="Je souhaite devenir membre du Registre Camerounais de Traumatologie"
                  style={{ cursor: "pointer" }}
                  data-bs-toggle="modal"
                  data-bs-target={"#itemModal-" + item.id}
                />
              </List.Item>
            )}
          />
        </Spin>
      </div>
    </div>
  );
};

const DemandeTag = (val: DemandeStatut) => {
  switch (val) {
    case "nouveau":
      return (
        <Tag
          icon={<FaArrowDown size={12} />}
          color="processing"
          className="rounded-pill"
          bordered={false}
        >
          <span> nouveau </span>
        </Tag>
      );
    case "en attente":
      return (
        <Tag
          icon={<FaClock size={12} />}
          color="processing"
          className="rounded-pill"
          bordered={false}
        >
          <span> en attente </span>
        </Tag>
      );
    case "accepte":
      return (
        <Tag
          icon={<FaCheck size={12} />}
          color="success"
          className="rounded-pill"
          bordered={false}
        >
          <span> accepte </span>
        </Tag>
      );
    case "rejete":
      return (
        <Tag
          icon={<FaWindowClose size={12} className="rounded-pill" />}
          color="error"
          className="rounded-pill"
          bordered={false}
        >
          <span> rejete </span>
        </Tag>
      );
    default:
      return (
        <Tag
          icon={<FaCheck size={12} />}
          color="default"
          className="rounded-pill"
          bordered={false}
        >
          <span> en attente </span>
        </Tag>
      );
  }
};

type DemandeStatut = "nouveau" | "en attente" | "accepte" | "rejete";
