import { Link, useHistory } from "react-router-dom";
import { ROUTES } from "../../../../routes";
import { AdminContainer } from "../../components/AdminContainer";
import { DossierDetails } from "../../components/dossier/DossierDetails";
import { FaUserAlt } from "react-icons/fa";
import { ButtonWithModal } from "../../../shared/ButtonWithModal";
import { notification } from "antd";
import { useEffect } from "react";
import { PatientForm } from "../../components/Patients/PatientForm";

export const AddDossierPage = () => {
  const router = useHistory();
  const { dossier, readOnly } = router.location.state
    ? (router.location.state as any)
    : {
        dossier: null,
        readOnly: false,
      };

  useEffect(() => {
    if (!dossier) {
      notification.error({
        message: "Erreur",
        description:
          "Veuillez selectionner un patient pour consulter son dossier",
      });
      router.push(ROUTES.PATIENT_PAGE.LIST_PATIENT);
    }
  }, [dossier, router]);

  return (
    <AdminContainer clicked="registre-all">
      <div className="row">
        <h2 className="col-md-6">
          <Link to={ROUTES.REGISTRE_PAGE.PRINCIPAL}>Registre principal</Link> |
          {"  "}
          <span className="">
            {"P" +
              dossier.patient.id +
              "FS" +
              dossier.formation_sanitaire.id +
              "D" +
              dossier.id}
          </span>
        </h2>
        <div className="col-md-6 ms-auto">
          <ButtonWithModal
            width={800}
            buttonText="Informations personnelles"
            buttonProps={{
              style: {
                backgroundColor: "#F5F8FF",
              },
              className: "btn rounded-pill",
              icon: (
                <FaUserAlt
                  style={{ marginRight: 5, marginBottom: 2 }}
                  size={20}
                />
              ),
            }}
            modalProps={{ title: `Informations personnelles du patient` }}
          >
            {(closeModal) => (
              <PatientForm
                key={"patient-details"}
                patient={dossier.patient}
                readOnly={true}
              />
            )}
          </ButtonWithModal>
        </div>
      </div>

      <DossierDetails />
    </AdminContainer>
  );
};
