import { Alert, Checkbox, Modal, Space } from "antd";
import { useEffect, useState } from "react";
import { DataTable } from "../../../shared/Table";
import styled from "@emotion/styled";
import { DossierEntity } from "../../../../entities/dossier.entity";
import { ButtonWithModal } from "../../../shared/ButtonWithModal";
import { FaPlus, FaSearch } from "react-icons/fa";
import { DOSSIER_COLUMNS } from "../../../../entities/dossier.entity";
import { DossierAjout } from "./DossierAjout";
import { fetchDossier } from "../../network";
import {
  DOSSIER_PC_MODE_TABS,
  DOSSIER_STATUT,
} from "../../../../shared/rubrique";
import { LegendItem } from "../../../shared/Layouts";
import { BS_BLUE } from "../../../../shared/colors";

const Container = styled.div``;

export const loadData = async (
  setIsLoading: (val: boolean) => void,
  setDossiers: (val: any) => void
) => {
  setIsLoading(true);
  <Alert message="processing the request..." />;
  setIsLoading(false);
};

export const DossierListComponent = ({
  praticien,
  formation_sanitaire,
  tampon,
  transfert,
  attente,
  deces,
  selectedPatient,
  title,
  createDossier,
}: {
  praticien?: number | string;
  formation_sanitaire?: number | string;
  tampon?: boolean;
  transfert?: boolean;
  attente?: boolean;
  deces?: boolean;
  selectedPatient?: number | string;
  title?: string;
  createDossier?: boolean;
}) => {
  const [isLoading, setIsLoading] = useState(true); //loader des dossiers fetcher
  const [dossierAllData, setAllDossiers] = useState<DossierEntity[]>([]); // tous les dossier
  const [dossierData, setDossiers] = useState<DossierEntity[]>([]); // dossiers filtrs
  const [traiterDossier, setTraiterDossier] = useState(false); // lorsqu'un medecin recupere un dossier
  const [selectedDossier, setSelectedDossier] = useState<any>(); // dossier selectionner pour le traitement
  const [reset, setReset] = useState(false); // effacer le filtre

  useEffect(() => {
    fetchDossier().then((data) => {
      if (data.success) {
        let dossiers = data.result;
        setAllDossiers(data.result);
        setDossiers(data.result);
        if (selectedPatient) {
          setDossiers(
            dossiers.filter((dos) => dos.patient.id === selectedPatient)
          );
        }
        if (praticien) {
          setDossiers(dossiers.filter((dos) => dos.praticien.id === praticien));
        }
        if (formation_sanitaire) {
          setDossiers(
            dossiers.filter(
              (dos) => dos.formation_sanitaire.id === formation_sanitaire
            )
          );
        }
        if (transfert) {
          setDossiers(dossiers.filter((dos) => dos.transfert === 1));
        }
        if (attente) {
          // dossier en instance de traitement
          setDossiers(
            dossiers.filter((dos) => dos.statut === DOSSIER_STATUT.anamnese)
          );
        }
        if (tampon) {
          setDossiers(dossiers.filter((dos) => dos.puffer === 1));
        }
        if (deces) {
          setDossiers(
            dossiers.filter((dos) => dos.statut === DOSSIER_STATUT.decede)
          );
        }
        setIsLoading(false);
      }
    });
  }, [
    deces,
    formation_sanitaire,
    praticien,
    tampon,
    transfert,
    selectedPatient,
    attente,
  ]);

  console.log("dossiers list ", dossierAllData);

  return (
    <Container>
      {/* Mettre un filtre sur la priode, le statut, filtre  checkbox aussi si possible */}
      <div className="row mb-3 accordion-collapse collapse" id="collapseLegend">
        <div className="" id="legend-registre-principal">
          {/* <h6>Legende</h6> */}
          <Space wrap style={{ fontSize: 11 }} size={"middle"}>
            {DOSSIER_PC_MODE_TABS.map((mode) => (
              <LegendItem
                color={mode.color}
                title={mode.title}
                name={mode.name}
              />
            ))}
          </Space>
        </div>
      </div>
      <div>
        <Modal
          open={traiterDossier}
          onCancel={() => setTraiterDossier(false)}
          footer={null}
          centered
          title="Traiter le dossier"
        >
          <DossierAjout
            key={"traiter-dossier-formulaire"}
            dossier={selectedDossier}
            closeModal={() => setTraiterDossier(false)}
          />
        </Modal>
        <DataTable
          loading={isLoading}
          data={reset ? dossierAllData : dossierData}
          columns={DOSSIER_COLUMNS(
            setIsLoading,
            setDossiers,
            loadData,
            setTraiterDossier,
            setSelectedDossier
          )}
          filterFunction={(dossier: DossierEntity, filterValue: string) =>
            dossier.patient.nom.includes(filterValue) ||
            dossier.patient.prenom.includes(filterValue) ||
            dossier.patient.nom.toLowerCase().includes(filterValue) ||
            dossier.patient.prenom.toLowerCase().includes(filterValue)
          }
          buttons={
            <Space className="ms-auto" size={"large"}>
              <i>
                <FaSearch color={BS_BLUE} />
              </i>
              <Checkbox
                name="registre-reset"
                className="ml-3"
                checked={reset}
                onChange={(e) => setReset(e.target.checked)}
              >
                Afficher tout
              </Checkbox>
              <Checkbox
                name="registre-legend"
                className="ml-3"
                data-bs-toggle="collapse"
                data-bs-target="#collapseLegend"
                aria-expanded="true"
                aria-controls="collapseLegend"
              >
                Legende
              </Checkbox>
              <div>
                {createDossier ? (
                  <ButtonWithModal
                    buttonText="Nouveau dossier"
                    buttonProps={{
                      style: {
                        backgroundColor: "lightgreen",
                        borderWidth: 0,
                        borderRadius: 15,
                      },
                      icon: (
                        <FaPlus
                          style={{ marginRight: 5, marginBottom: 2 }}
                          size={20}
                        />
                      ),
                    }}
                    modalProps={{ title: `Cration d'un dossier` }}
                  >
                    {(closeModal) => (
                      <DossierAjout
                        key={"dossier-ajout-form"}
                        closeModal={closeModal}
                      />
                    )}
                  </ButtonWithModal>
                ) : (
                  ""
                )}
              </div>
            </Space>
          }
        />
      </div>
    </Container>
  );
};
