import { Form, notification, Input, DatePicker, Space, Spin } from "antd";
import { CODE_RUBRIQUES } from "../../../../shared/rubrique";
import {
  DateLayout,
  FullSelectLayout,
  InputLayout,
  RadioLayout,
} from "../../../shared/Layouts";
import { useState, useEffect } from "react";
import { EltRubriqueEntity } from "../../../../entities/eltRubrique.entity";
import { PaysEntity } from "../../../../entities/pays.entity";
import { fetchPays, fetchEltRubrique, createPatient } from "../../network";
import { PatientEntity } from "../../../../entities/patient.entity";
import { useHistory } from "react-router-dom";
import { ROUTES } from "../../../../routes";
import { getAge } from "../../../shared/DateToFrench";
import { FaExclamationCircle } from "react-icons/fa";
import { formRules } from "../../../../shared/formRules";

const sexeItems = [
  { id: "M", code: "M", designation: "Homme", description: "" },
  { id: "F", code: "M", designation: "Femme", description: "" },
] as EltRubriqueEntity[];

export const PatientInfoForm = ({
  patient,
  readOnly,
}: {
  patient?: PatientEntity;
  readOnly?: boolean;
}) => {
  const router = useHistory();
  const [form] = Form.useForm();
  const dateValue = Form.useWatch("date_naissance", form);
  const [isLoadingForm, setIsLoadingForm] = useState(false);
  const [pays, setPays] = useState<PaysEntity[]>([]);
  const [eltRubriques, setEltRubriques] = useState<EltRubriqueEntity[]>([]);
  const [change, setChange] = useState(readOnly);

  console.log("patient info form readOnly ?", change, patient, readOnly);

  const formatDate = (dateValue: string) => {
    // format date to YYYYMMDD format
    const dateToFormat = new Date(dateValue);
    return `${dateToFormat.getFullYear()}${
      dateToFormat.getMonth() + 1 < 10
        ? "0" + (dateToFormat.getMonth() + 1)
        : dateToFormat.getMonth() + 1
    }${
      dateToFormat.getDate() < 10
        ? "0" + dateToFormat.getDate()
        : dateToFormat.getDate()
    }`;
  };

  const getAgePatient = getAge(formatDate(dateValue?.toLocaleString()));

  const isMineur =
    getAgePatient &&
    !(getAgePatient?.unit === "ans" && getAgePatient?.value > 15);

  useEffect(() => {
    fetchPays().then((data) => {
      if (data.success) setPays(data.result);
    });
    fetchEltRubrique().then((data) => {
      if (data.success) setEltRubriques(data.result);
    });
  }, []);

  const getNationalite = () => {
    const patientNation = pays.find(
      (nat: any) => nat.id === patient?.nationalite
    );
    return patientNation?.designation;
  };

  const getReligion = () => {
    const patientReligion = eltRubriques.find(
      (elt) => elt.id === patient?.religion
    );
    return patientReligion?.designation;
  };

  const getProfession = (professionId: any) => {
    const patientProfession = eltRubriques.find(
      (elt) => elt.id === professionId
    );
    return patientProfession?.designation;
  };

  const PatientFormReadOnly = () => {
    return (
      <Form
        name="patient-read-form"
        key="patient-read-form"
        className="col-md-10"
        labelCol={{ span: 8 }}
      >
        <div className="row">
          <Form.Item label="Nom" name="nom">
            <Input
              defaultValue={patient?.nom}
              className="text-dark text-uppercase"
              disabled
              style={{ fontFamily: "revert", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Prenom" name="prenom">
            <Input
              defaultValue={patient?.prenom}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <RadioLayout
            label="Sexe"
            name="sexe"
            defaultValue={patient?.sexe}
            items={sexeItems}
            rules={[formRules.ruleNoRequired]}
            disabled
          />
          <Form.Item label="Date de naissance" name="date_naissance">
            <Input
              defaultValue={patient?.date_naissance}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Adresse electronique" name="adresse_mail">
            <Input
              defaultValue={patient?.adresse_mail}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Adresse" name="adresse">
            <Input
              defaultValue={patient?.adresse}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Numro de tlephone" name="telephone">
            <Input
              defaultValue={patient?.telephone}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Nationalite" name="nationalite">
            <Input
              defaultValue={getNationalite()}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Religion" name="religion">
            <Input
              defaultValue={getReligion()}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Profession" name="profession">
            <Input
              defaultValue={getProfession(patient?.profession)}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <div className="mt-3">
            <span
              className="mx-3 text-primary"
              style={{ fontFamily: "monospace" }}
            >
              {" "}
              Personne  contacter en cas d'urgence{" "}
            </span>
            <hr />
          </div>
          <Form.Item label="Nom de la personne" name="nom_urgence">
            <Input
              defaultValue={patient?.nom_urgence}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <Form.Item label="Numro de telephone" name="telephone_urgence">
            <Input
              defaultValue={patient?.telephone}
              className="text-dark"
              disabled
              style={{ fontFamily: "revert-layer", fontSize: 17 }}
            />
          </Form.Item>
          <div className="ms-auto col-md-3 d-none">
            <button
              className="btn btn-primary rounded-pill"
              onClick={() => {
                setChange(false);
              }}
            >
              Modifier
            </button>
          </div>
        </div>
      </Form>
    );
  };

  const PatientFormEditable = () => {
    return (
      <Spin spinning={isLoadingForm}>
        <Form
          name="patient-info-form"
          key="patient-info-form"
          form={form}
          className="col-md-10"
          labelCol={{ span: 8 }}
          scrollToFirstError
          onFinish={async (data) => {
            setIsLoadingForm(true);
            let dataToPost = data;
            dataToPost.date_naissance = new Date(
              data.date_naissance
            ).toLocaleString();
            await createPatient(dataToPost).then((data) => {
              if (data.success) {
                notification.success({
                  message: "Succs",
                  description: data.message,
                });
                router.push(ROUTES.PATIENT_PAGE.LIST_PATIENT);
              } else {
                notification.error({
                  message: "Erreur",
                  description: data.message,
                });
              }
            });
            setIsLoadingForm(false);
          }}
        >
          <div>
            <InputLayout
              label="NUI"
              placeholder="Numro d'Identifiant Unique"
              name="niu"
              rules={[formRules.ruleNoRequired]}
              value={patient?.niu}
              disabled={readOnly}
            />
            <InputLayout
              label="Nom"
              placeholder="Nom du patient"
              name="nom"
              rules={[formRules.ruleRequired]}
              value={patient?.nom}
              disabled={readOnly}
            />
            <InputLayout
              label="Prnoms"
              name="prenom"
              rules={[formRules.ruleRequired]}
              value={patient?.prenom}
              disabled={readOnly}
              placeholder="Prnoms du patient"
            />
            <RadioLayout
              label="Sexe"
              name="sexe"
              items={sexeItems}
              rules={[formRules.ruleRequired]}
              defaultValue={patient?.sexe}
            />
            <DateLayout
              label="Date de naissance"
              name="date_naissance"
              rules={[formRules.ruleRequired]}
              value={patient?.date_naissance}
              disabled={readOnly}
            />
            {isMineur ? (
              <span className="mb-3 text-danger">
                <i>
                  <FaExclamationCircle color="red" className="mx-3" />
                </i>
                Ce patient est mineur. Les informations suivantes sont celles de
                son tuteur
              </span>
            ) : (
              <span> </span>
            )}
            <InputLayout
              label="Adresse electronique"
              name="adresse_mail"
              className="mt-3"
              rules={[formRules.ruleNoRequired]}
              value={patient?.adresse_mail}
              disabled={readOnly}
              placeholder="Email du patient ou du tuteur"
            />
            <InputLayout
              label="Numro de tlephone"
              name="telephone"
              rules={[formRules.ruleRequired]}
              value={patient?.telephone}
              disabled={readOnly}
              placeholder="Telephone du patient ou du tuteur"
            />
            <FullSelectLayout
              label="Nationalit"
              name="nationalite"
              key={"patient-nationalite"}
              items={pays}
              rules={[formRules.ruleNoRequired]}
              value={patient?.nationalite}
              disabled={readOnly}
              placeholder="Nationalit du patient ou du tuteur"
            />
            <FullSelectLayout
              label="Religion"
              name="religion"
              key={"patient-religion"}
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.RELIGION
              )}
              rules={[formRules.ruleNoRequired]}
              placeholder="Religion du patient ou du tuteur"
            />
            <FullSelectLayout
              label="Profession"
              name="profession"
              key={"patient-profession"}
              items={eltRubriques.filter(
                (elt) => elt.rubrique === CODE_RUBRIQUES.PROFESSION
              )}
              rules={[formRules.ruleNoRequired]}
              placeholder="Profession du patient ou du tuteur"
            />
            <div className="mt-3">
              <span
                className="mx-3 text-primary"
                style={{ fontFamily: "monospace" }}
              >
                {" "}
                Personne  contacter en cas d'urgence{" "}
              </span>
              <hr />
            </div>
            <InputLayout
              label="Nom de la personne"
              name="nom_urgence"
              rules={[formRules.ruleRequired]}
              value={patient?.nom_urgence}
              disabled={readOnly}
              placeholder="Nom de la personne  contacter en cas d'urgence"
            />
            <InputLayout
              label="Numero de tlephone"
              name="telephone_urgence"
              rules={[formRules.ruleRequired]}
              value={patient?.telephone_urgence}
              disabled={readOnly}
              placeholder="Telephone de la personne  contacter en cas d'urgence"
            />
            <div className="row">
              <Space className="ms-auto col-md-4">
                <button
                  type="button"
                  className="btn btn-outline-secondary ml-3 rounded-pill"
                  onClick={() => router.push(ROUTES.PATIENT_PAGE.LIST_PATIENT)}
                >
                  Annuler
                </button>
                <button type="submit" className="btn btn-primary rounded-pill">
                  Enregistrer
                </button>
              </Space>
            </div>
          </div>
        </Form>
      </Spin>
    );
  };

  return change ? <PatientFormReadOnly /> : <PatientFormEditable />;
};
