import { DossierEntity } from "../../../../entities/dossier.entity";
import { Tabs, TabsProps, notification } from "antd";
import { useEffect, useState } from "react";
import {
  fetchDossier,
  fetchEltDossier,
  fetchEltRubrique,
  fetchRubrique,
} from "../../network";
import { EltRubriqueEntity } from "../../../../entities/eltRubrique.entity";
import { useHistory } from "react-router-dom";
import { RubriqueEntity } from "../../../../entities/rubrique.entity";
import {
  FaClinicMedical,
  FaNotesMedical,
  FaRegArrowAltCircleRight,
  FaRegCheckCircle,
} from "react-icons/fa";
import { EltDossierEntity } from "../../../../entities/eltDossier.entity";
import { DossierAnamnese } from "./steps/DossierAnamnese";
import { DossierTraitement } from "./steps/DossierTraitement";
import { DossierSuivi } from "./steps/DossierSuivi";
import { DossierRehabilitation } from "./steps/DossierRehabilitation";

export const DossierDetails = () => {
  const router = useHistory();
  const state = router.location.state as any;
  const [dossier, setDossier] = useState(state.dossier);
  const [activeTag, setActiveTag] = useState(dossier.step);
  const [eltRubriques, setEltRubriques] = useState<EltRubriqueEntity[]>([]);
  const [rubriques, setRubriques] = useState<RubriqueEntity[]>([]);
  const [eltDossiers, setEltDossiers] = useState<EltDossierEntity[] | any>([]);
  const [reloadData, setReloadData] = useState(false);

  useEffect(() => {
    fetchRubrique().then((data) => {
      if (data.success) setRubriques(data.result);
    });
    fetchEltRubrique().then((data) => {
      if (data.success) setEltRubriques(data.result);
    });
    fetchEltDossier().then((data) => {
      if (data.success) {
        const elts = data.result.filter(
          (eltDos) => eltDos.dossier === dossier.id
        );
        setEltDossiers(elts);
      }
    });
    if (reloadData) {
      console.log("handle reload changes");
      fetchDossier(dossier.id).then((data) => {
        console.log("reload dossier after changes : ", data.result);
        setReloadData(false);
        if (data.success) {
          setDossier(data.result);
        } else {
          notification.error({
            message: "Erreur",
            description:
              "Un problme est survenu. Impossible d'afficher' le dossier.",
          });
        }
      });
    }
  }, [dossier, reloadData]);

  const items: TabsProps["items"] = [
    {
      key: "1",
      label: (
        <span>
          <FaNotesMedical className="mx-3" size={28} />
          Anamnese et diagnostic
        </span>
      ),
      children: (
        <DossierAnamnese
          dossier={dossier}
          eltDossiers={eltDossiers}
          eltRubriques={eltRubriques}
          rubriques={rubriques}
          setActiveTag={setActiveTag}
          reloadData={setReloadData}
        />
      ),
    },
    {
      key: "2",
      label: (
        <span>
          <FaClinicMedical className="mx-3" size={28} />
          Traitement
        </span>
      ),
      children: <DossierTraitement />,
      disabled: dossier.statut < 20 ? true : false,
    },
    {
      key: "3",
      label: (
        <span>
          <FaRegArrowAltCircleRight className="mx-3" size={28} />
          Suivi post opratoire
        </span>
      ),
      children: <DossierSuivi />,
      disabled: dossier.statut < 30 ? true : false,
    },
    {
      key: "4",
      label: (
        <span>
          <FaRegCheckCircle className="mx-3" size={28} />
          Rhabilitation
        </span>
      ),
      children: <DossierRehabilitation />,
      disabled: dossier.statut < 40 ? true : false,
    },
  ];

  return (
    <section className="section profile">
      <div className="row">
        <div className="card card-body">
          <Tabs
            defaultActiveKey="1"
            items={items}
            activeKey={activeTag}
            onTabClick={(key) => {
              console.log("in navigation tag dossier ", key, dossier.step);
              setReloadData(true);
              if (key <= dossier.step) setActiveTag(key);
            }}
          />
        </div>
      </div>
    </section>
  );
};

export type DossierState = {
  dossier: DossierEntity;
  readOnly: boolean;
};

export const getPayload = (
  dossier: any,
  dossierStep: number,
  codeRubrique: string,
  multiple: boolean
) => {
  const payload = dossier.payload;
  let values: any[] = [];
  let value;
  if (payload.length > 0 && dossier.statut > dossierStep) {
    if (multiple) {
      payload
        .filter((eltDossier: any) => eltDossier.rubrique === codeRubrique)
        ?.map((elt: any) => {
          values.push(elt.valeur);
        });
    } else {
      value = payload.find(
        (eltDossier: any) => eltDossier.rubrique === codeRubrique
      )?.valeur;
    }
  }

  return multiple ? values : value;
};
