import { FaChevronRight } from "react-icons/fa";
import { useHistory } from "react-router-dom";

export const MiniCard = ({ card }: { card: any }) => (
  <div className="card p-0 mb-1">
    <div className="card-body text-center p-1">
      <div className="card-text row align-items-end">
        <span className="col-md-3 pb-1">
          <i className="card-icon rounded-circle">{card.icon}</i>
        </span>
        <div className="col-md-9 mx-0">
          <span>{card.title}</span>
          <h3 className="mb-0"> {card.value}</h3>
        </div>
      </div>
    </div>
  </div>
);

export const CardDashboard = ({
  card,
  size,
}: {
  card: any;
  size?: number | string;
}) => {
  const router = useHistory();
  return (
    <div
      className="card p-0"
      style={{ cursor: "pointer", width: size ? size : "23%", marginRight: 18 }}
      onClick={() => router.push(card.link)}
    >
      <span className="card-header m-0" style={{ backgroundColor: card.color }}>
        {" "}
      </span>
      <div className="card-body text-center ">
        <div className="card-text row">
          <span className="col-md-3">
            <i className="card-icon rounded-circle">{card.icon()}</i>
          </span>
          <div className="col-md-9 mx-0">
            <h2 className="mb-0"> {card.value} </h2>
            <span>{card.description}</span>
          </div>
        </div>
      </div>
      <span
        className="card-footer d-flex"
        style={{ backgroundColor: card.color }}
      >
        <FaChevronRight className="ms-auto" color="white" size={10} />{" "}
      </span>
    </div>
  );
};
