import { Form, Spin } from "antd";
import form from "antd/es/form";
import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { useState } from "react";
import { RubriqueTitle } from "../../../../shared/Layouts";

export const FormAntecedent = ({
  dossier,
  reloadData,
  eltRubriques,
  activeKey,
  setActiveKey,
}: {
  dossier: any;
  reloadData: (val: boolean) => void;
  eltRubriques: EltRubriqueEntity[];
  activeKey: number;
  setActiveKey: (tag: number) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);
  const payload = dossier.payload;

  return (
    <Spin spinning={isLoadingForm}>
      <Form
        name={"acq-antecedent-form-" + dossier.id}
        key={"acq-antecedent-form-" + dossier.id}
        form={form}
        className={
          activeKey === DOSSIER_STATUT.antecedent ? "collapse show" : "d-none"
        }
        id={"acq-antecedent-" + dossier.id}
        labelCol={{ span: 8 }}
        scrollToFirstError
      >
        <div className="card card-body">
          <RubriqueTitle
            key={"dossier-acq-antecedent-" + dossier.id}
            title="Antecedents mdicaux"
          />
          <div className="">
            <span>Coming soon</span>
          </div>
        </div>
      </Form>
    </Spin>
  );
};
