import { Form, FormInstance } from "antd";
import {
  DateLayout,
  FullSelectLayout,
  InputLayout,
} from "../../../../shared/Layouts";
import { FaExchangeAlt } from "react-icons/fa";
import { BS_BLUE, BS_WARNING } from "../../../../../shared/colors";

export const FormTransfert = ({ formRef }: { formRef: FormInstance<any> }) => {
  const localFS = localStorage.getItem("formationSanitaires");
  const formationSanitaires = localFS ? JSON.parse(localFS) : [];

  const localPraticiens = localStorage.getItem("praticiens");
  const praticiens = localPraticiens ? JSON.parse(localPraticiens) : [];

  return (
    <Form
      name="acq-transfert-form"
      key="acq-transfert-form"
      form={formRef}
      id="acq-transfert"
      labelCol={{ span: 8 }}
      scrollToFirstError
    >
      <div className="row">
        <i className="col-md-1">
          <FaExchangeAlt size={24} color={BS_BLUE} />
        </i>
        <strong className="col-md-8">
          <h5 style={{ fontFamily: "monospace" }} className="text-primary">
            Transfrer le dossier
          </h5>{" "}
        </strong>
      </div>
      <hr />
      <div>
        <DateLayout
          label="Date du transfert"
          name="transfert_date"
          key={"transfert_date"}
          formRef={formRef}
        />
        <FullSelectLayout
          label="Formations sanitaires"
          key="transfert_formation_sanitaire"
          name="formation_sanitaires"
          items={formationSanitaires}
          size={12}
          multiple={true}
          placeholder="Selectionner la formation sanitaire"
        />
        <FullSelectLayout
          label="Praticiens"
          key="transfert-praticien"
          name="praticiens"
          items={praticiens}
          size={12}
          multiple={true}
          placeholder="Selectionner le medecin  qui transfrer le dossier"
        />
        <InputLayout
          label="Motif"
          name="transfert_motif"
          size={12}
          key={"transfert_motif"}
        />
        <InputLayout
          label="Notes"
          name="transfert_notes"
          multiple={true}
          size={12}
          key={"transfert_notes"}
        />
      </div>
    </Form>
  );
};
