import { Form, FormInstance } from "antd";
import { DateLayout, InputLayout } from "../../../../shared/Layouts";
import { FaBookDead } from "react-icons/fa";
import { BS_BLUE } from "../../../../../shared/colors";

export const FormDeces = ({ formRef }: { formRef: FormInstance<any> }) => {
  return (
    <Form
      name="acq-deces-form"
      key="acq-deces-form"
      form={formRef}
      id="acq-deces"
      labelCol={{ span: 8 }}
      scrollToFirstError
    >
      <div className="row">
        <i className="col-md-1">
          <FaBookDead size={24} color={BS_BLUE} />
        </i>
        <strong className="col-md-8">
          <h5 style={{ fontFamily: "monospace" }} className="text-primary">
            Dcs
          </h5>
        </strong>
      </div>
      <hr />
      <div>
        <DateLayout
          label="Date du deces"
          name="deces_date"
          key={"deces_date"}
          formRef={formRef}
        />
        <InputLayout
          label="Motif"
          name="deces_motif"
          size={12}
          key={"deces_motif"}
        />
        <InputLayout
          label="Notes"
          name="deces_notes"
          multiple={true}
          size={12}
          key={"deces_notes"}
        />
      </div>
    </Form>
  );
};
