import { Document, Page, StyleSheet, View, Text } from "@react-pdf/renderer";
import { CODE_RUBRIQUES, getQualification } from "../../shared/rubrique";
import { ORGANISMES } from "../../shared/constants";

const documentStyle = StyleSheet.create({
  page: { flexDirection: "column", flex: 1 },
  section: { margin: 10, padding: 10, flexGrow: 1 },
  textLabel: { marginLeft: 5 },
  textValue: { fontWeight: "thin" },
  textLine: { marginLeft: 25, marginTop: 10, marginBottom: 5 },
});

export const PDFDossierComplet = ({ dossier }: { dossier?: any }) => {
  console.log(
    "i'm rendering well in show dossier page as PDF. Here it's me !",
    dossier
  );

  const DocumentInput = ({
    label,
    value,
    tag,
  }: {
    label: string;
    value: string;
    tag: string;
  }) => (
    <Text key={tag + "-" + label}>
      <Text style={documentStyle.textLabel}>{label} </Text>{" "}
      <Text style={documentStyle.textValue}>{value}</Text>
    </Text>
  );

  return (
    <Document>
      <Page size="A4" style={documentStyle.page}>
        <View style={documentStyle.section}>
          <Text> IDENTITE DU PRATICIEN </Text>
          <Text style={documentStyle.textLine}>
            {" "}
            Nom : {dossier.praticien.nom}{" "}
          </Text>
          <Text style={documentStyle.textLine}>
            {" "}
            Prenom : {dossier.praticien.prenom}{" "}
          </Text>
          <Text style={documentStyle.textLine}>
            {" "}
            Qualification :{" "}
            {
              getQualification(dossier.praticien.qualification)?.designation
            }{" "}
          </Text>
          <Text style={documentStyle.textLine}>
            {" "}
            Annes d'experiences : {dossier.praticien.nb_experience}
          </Text>
          <Text style={documentStyle.textLine}>
            {" "}
            Adhrant  un rseau de traumatologie :{" "}
            {dossier.praticien.organismes?.length === 0
              ? "aucun"
              : dossier.praticien.organismes.map(
                  (user_org: number | string, index: number) =>
                    ORGANISMES.find(
                      (org) =>
                        org.id === user_org &&
                        org.type_organisme ===
                          CODE_RUBRIQUES.RESEAU_TRAUMATOLOGIE
                    )
                      ? ORGANISMES.find(
                          (org) =>
                            org.id === user_org &&
                            org.type_organisme ===
                              CODE_RUBRIQUES.RESEAU_TRAUMATOLOGIE
                        )?.designation + ", "
                      : ""
                )}
          </Text>
        </View>
        <View style={documentStyle.section}>
          <Text> FORMATION SANITAIRE </Text>
        </View>
      </Page>
    </Document>
  );
};
