import { useHistory } from "react-router-dom";
import { Space, Tooltip, notification } from "antd";
import {
  FaArrowAltCircleRight,
  FaEdit,
  FaExchangeAlt,
  FaInfoCircle,
  FaNotesMedical,
  FaPenAlt,
} from "react-icons/fa";
import { BS_BLUE, GREEN_DARK, GREY_LIGHT, YELLOW_DARK } from "../shared/colors";
import { ROUTES } from "../routes";
import { PatientEntity } from "./patient.entity";
import { dossierStatutTag, modePCTag } from "../modules/shared/Layouts";
import { MedecinEntity } from "./medecin.entity";
import { FormationSanitaireEntity } from "./formationSanitaire";
import { DOSSIER_STATUT } from "../shared/rubrique";
import { useState } from "react";

export class DossierEntity {
  _id: string;
  code: string;
  patient: PatientEntity;
  sexe!: string;
  dateEntree!: string;
  dateSortie!: string;
  statut!: number;
  visibilite!: boolean;
  transfert!: boolean;
  transfertNb!: number;
  puffer!: boolean;
  pufferNb!: number;
  formation_sanitaire!: FormationSanitaireEntity;
  disabled: number;
  lastUpdate!: string;
  mode!: string;
  step!: string;
  praticien!: MedecinEntity;
  date_traumatisme!: string;
  date_enregistrement!: string;
  payload!: any;

  constructor(dossier: DossierEntity) {
    this._id = dossier._id;
    this.code = dossier.code;
    this.patient = dossier.patient;
    this.dateEntree = dossier.dateEntree;
    this.dateSortie = dossier.dateSortie;
    this.statut = dossier.statut;
    this.visibilite = dossier.visibilite;
    this.transfert = dossier.transfert;
    this.transfertNb = dossier.transfertNb;
    this.puffer = dossier.puffer;
    this.pufferNb = dossier.pufferNb;
    this.formation_sanitaire = dossier.formation_sanitaire;
    this.disabled = dossier.disabled;
    this.lastUpdate = dossier.lastUpdate;
    this.disabled = dossier.disabled;
  }
}

export const DOSSIER_COLUMNS = (
  setIsLoading: (val: boolean) => void,
  setDossiers: (val: any) => void,
  loadData: (
    setIsLoading: (val: boolean) => void,
    setDossiers: (val: any) => void
  ) => void,
  setTraiterDossier?: (val: any) => void,
  setSelectedDossier?: (val: any) => void
) => {
  const router = useHistory();

  return [
    {
      title: "Nom",
      key: "dossier-nom",
      render: (cell: any, row: any) => {
        return (
          <div>
            <span className="text-uppercase">{cell.patient.nom + " "}</span>
            {cell.patient.prenom}{" "}
          </div>
        );
      },
    },
    {
      title: "Date naissance",
      key: "dossier-date_naissance",
      render: (cell: any, row: any) => {
        return <span>{cell.patient.date_naissance}</span>;
        // return <span>17/07/96</span>;
      },
    },
    {
      title: "Formation sanitaire",
      key: "dossier-formation_sanitaire",
      render: (cell: any, row: any) => {
        return <span>{row.formation_sanitaire.designation} </span>;
        // return <span>Polyclinique de Tsinga </span>;
      },
    },
    {
      title: "N Dossier",
      key: "code-dossier",
      render: (cell: any, row: any) => {
        return (
          <span>
            {row.patient.id + "." + row.formation_sanitaire.id + "." + row.id}
            {/* {"P" +
              row.patient.id +
              "FS" +
              row.formation_sanitaire.id +
              "D" +
              row.id} */}
            {/* {row.formation_sanitaire.id + "Y" + row.patient.id + "P" + row.id}{" "} */}
            {/* 045Y012P002 */}
          </span>
        );
      },
    },
    {
      title: "Praticien",
      key: "dossier-praticien",
      render: (cell: any, row: any) => {
        return <span>{row.praticien.nom + " " + row.praticien.prenom} </span>;
        // return <span>Lekina Florent </span>;
      },
    },
    {
      title: "Prise en charge",
      key: "dossier-mode",
      render: (cell: any, row: any) => {
        return <div className="d-flex mx-auto"> {modePCTag(row.mode)} </div>;
      },
    },
    {
      title: "Statut",
      key: "Etape",
      render: (cell: any, row: any) => {
        return (
          <div className="d-flex mx-auto"> {dossierStatutTag(row.statut)} </div>
        );
      },
    },
    {
      title: "Actions",
      key: "dossier-actions",
      render: (cell: any, row: any) => {
        return (
          <Space>
            {row.statut === DOSSIER_STATUT.clos ? ( // dossier clos
              // {/* nouveau dossier */}
              <Tooltip title="Nouveau dossier">
                <FaNotesMedical
                  size={24}
                  color={
                    row.statut === DOSSIER_STATUT.decede
                      ? GREY_LIGHT
                      : GREEN_DARK
                  }
                  style={{ cursor: "pointer" }}
                  onClick={async (data) => {
                    if (row.statut === DOSSIER_STATUT.decede) {
                      // patient decede
                      notification.info({
                        message: "Info",
                        description:
                          "Ce patient est dcde. Impossible de creer un nouveau dossier.",
                      });
                    }
                  }}
                />
              </Tooltip>
            ) : row.statut === DOSSIER_STATUT.anamnese ? (
              // instance de dossier ouvert mais en attente de traitement
              // {/*prendre le dossier en traitement*/}
              <Tooltip title="Traiter le dossier">
                <FaArrowAltCircleRight
                  size={24}
                  color={
                    row.statut === DOSSIER_STATUT.decede
                      ? GREY_LIGHT
                      : GREEN_DARK
                  }
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setSelectedDossier?.(row);
                    setTraiterDossier?.(true);
                  }}
                />
              </Tooltip>
            ) : (
              <Tooltip title="Editer le dossier">
                <FaEdit
                  size={24}
                  color={
                    row.statut === DOSSIER_STATUT.decede
                      ? GREY_LIGHT
                      : GREEN_DARK
                  }
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    if (row.statut === DOSSIER_STATUT.decede) {
                      // patient decede
                      notification.warning({
                        message: "Info",
                        description:
                          "Ce patient est dcde. Impossible d'editer le dossier ou d'en creer un nouveau.",
                      });
                    } else {
                      router.push({
                        pathname: ROUTES.REGISTRE_PAGE.ADD_DOSSIER,
                        state: {
                          dossier: row,
                          readOnly: false,
                        },
                      });
                    }
                  }}
                />
              </Tooltip>
            )}
            {/* transfrer le dossier */}
            <Tooltip title="Transfrer le dossier">
              <FaExchangeAlt
                size={24}
                color={
                  row.statut === DOSSIER_STATUT.decede ||
                  row.statut === DOSSIER_STATUT.clos ||
                  row.statut === DOSSIER_STATUT.anamnese
                    ? GREY_LIGHT
                    : BS_BLUE
                }
                style={{ cursor: "pointer" }}
                onClick={() => {
                  if (row.patient.statut === 2) {
                    // patient decede
                    notification.warning({
                      message: "Info",
                      description:
                        "Ce patient est dcde. Impossible de creer un nouveau dossier.",
                    });
                  }
                }}
              />
            </Tooltip>
            {/* editer les documents */}
            <Tooltip title="Editer les documents">
              <FaPenAlt
                size={24}
                color={
                  row.statut === DOSSIER_STATUT.decede
                    ? GREY_LIGHT
                    : YELLOW_DARK
                }
                style={{ cursor: "pointer" }}
                onClick={() => console.log("open patient form modal")}
              />
            </Tooltip>
            {/* consulter le dossier complet */}
            <Tooltip title="Dossier complet">
              <FaInfoCircle
                size={24}
                color="#FF4D4F"
                style={{ cursor: "pointer" }}
                onClick={() => {
                  // render medical record in pdf file
                  notification.info({
                    message: "To do",
                    description: "Print medical record as file",
                  });
                  router.push({
                    pathname: ROUTES.REGISTRE_PAGE.SHOW_DOSSIER,
                    state: {
                      dossier: row,
                    },
                  });
                }}
              />
            </Tooltip>
          </Space>
        );
      },
    },
  ];
};
