import { Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  SwitchLayout,
  RubriqueNavigate,
  FullSelectLayout,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";
import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";

export const FormMotif = ({
  dossier,
  reloadData,
  eltRubriques,
  activeKey,
  setActiveKey,
}: {
  dossier: any;
  reloadData: (val: boolean) => void;
  eltRubriques: EltRubriqueEntity[];
  // eltDossiers: any; //Dossier entity
  activeKey: number;
  setActiveKey: (tag: number) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);
  const payload = dossier.payload;

  const TYPE_ACCIDENT = eltRubriques.filter(
    (elt) => elt.rubrique === CODE_RUBRIQUES.TYPE_ACCIDENT
  );
  const MECANISME_TRAUMATISME = eltRubriques.filter(
    (elt) => elt.rubrique === CODE_RUBRIQUES.MECANISME_TRAUMATISME
  );

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name={"acq-motif-form-" + dossier.id}
        key={"acq-motif-form-" + dossier.id}
        form={form}
        className={
          activeKey === DOSSIER_STATUT.motif ? "collapse show" : "d-none"
        }
        id={"acq-motif-" + dossier.id}
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          // let dataToSend: any = form.getFieldsValue();
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.histoire,
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DOULEURS_LOCALISEES,
                valeur: data.motif_douleurs_local
                  ? data.motif_douleurs_local
                  : true,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.IMPOTENCE_FONCTIONNELLE,
                valeur: data.motif_impotence ? data.motif_impotence : false,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.TYPE_ACCIDENT,
                valeur: [data.motif_type_accident],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.MECANISME_TRAUMATISME,
                valeur: [data.motif_mecanisme],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          await completeDossier(dataToSend)
            .then(async (data) => {
              if (data.success) {
                reloadData(true);
                notification.success({
                  message: "Succs",
                  description: data.message,
                });
                reloadData(true);
                setActiveKey(DOSSIER_STATUT.histoire);
              } else {
                notification.error({
                  message: "Erreur",
                  description: data.message,
                });
              }
            })
            .catch((err) => {
              notification.error({
                message: "Erreur",
                description: "Impossible de creer l'element du dossier",
              });
            });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle
            key={"dossier-acq-motif-" + dossier.id}
            title="Motif de consultation"
          />
          <div className="">
            <SwitchLayout
              label="Traumatisme avec douleurs localises"
              name="motif_douleurs_local"
              key="motif_douleurs_local"
              // valuePropName="checked"
              value={
                payload.length > 0 && dossier.statut >= DOSSIER_STATUT.motif
                  ? payload.find(
                      (eltDossier: any) =>
                        eltDossier.rubrique ===
                        CODE_RUBRIQUES.DOULEURS_LOCALISEES
                    )?.valeur === 1
                    ? true
                    : false
                  : null
              }
              disabled={dossier.statut > DOSSIER_STATUT.motif ? true : false}
            />
            <SwitchLayout
              label="Impotence fonctionnelle"
              name="motif_impotence"
              key="motif_impotence"
              // valuePropName="checked"
              value={
                payload.length > 0 && dossier.statut >= DOSSIER_STATUT.motif
                  ? payload.find(
                      (eltDossier: any) =>
                        eltDossier.rubrique ===
                        CODE_RUBRIQUES.IMPOTENCE_FONCTIONNELLE
                    )?.valeur === 1
                    ? true
                    : false
                  : null
              }
              disabled={dossier.statut > DOSSIER_STATUT.motif ? true : false}
            />
            <FullSelectLayout
              size={12}
              label="Type d'accident"
              name="motif_type_accident"
              key="motif_type_accident"
              items={TYPE_ACCIDENT}
              rules={[formRules.ruleRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.motif,
                CODE_RUBRIQUES.TYPE_ACCIDENT,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.motif ? true : false}
            />
            <FullSelectLayout
              size={12}
              label="Mcanisme ayant caus le traumatisme"
              key="motif_mecanisme"
              name="motif_mecanisme"
              items={MECANISME_TRAUMATISME}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.motif,
                CODE_RUBRIQUES.MECANISME_TRAUMATISME,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.motif ? true : false}
            />
          </div>
          {dossier.statut > DOSSIER_STATUT.motif ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-primary ml-3 rounded-pill"
                onClick={() => setActiveKey(DOSSIER_STATUT.histoire)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-motif" />
          )}
        </div>
      </Form>
    </Spin>
  );
};
