import { Link, useHistory } from "react-router-dom";
import { AdminContainer } from "../../components/AdminContainer";
import { PatientForm } from "../../components/Patients/PatientForm";
import { ROUTES } from "../../../../routes";

export const ShowPatientPage = () => {
  const router = useHistory();
  const state = router.location.state ? (router.location.state as any) : null;

  return (
    <AdminContainer clicked="patients-list">
      <h2>
        <Link to={ROUTES.PATIENT_PAGE.LIST_PATIENT}>Liste des patients</Link> |
        Patient 000Y00 <span className="text-primary">{state?.patient.id}</span>
      </h2>
      <div className="card mt-0">
        <div className="card-body">
          <PatientForm patient={state?.patient} readOnly={state?.readOnly} />
        </div>
      </div>
    </AdminContainer>
  );
};
