import { Badge, Space, Tag } from "antd";
import {
  FaArrowDown,
  FaCheck,
  FaClock,
  FaInfoCircle,
  FaWindowClose,
} from "react-icons/fa";
import { Link, useHistory } from "react-router-dom";
import { ROUTES } from "../routes";
import { GREY_LIGHT } from "../shared/colors";
import { CODE_RUBRIQUES } from "../shared/rubrique";
import { OrganismeEntity } from "./organisme.entity";

export class MedecinEntity {
  _id: string;
  code!: string;
  nom: string;
  prenom: string;
  sexe!: boolean;
  date_naissance!: string;
  adresse_mail!: string;
  adresse!: string;
  telephone!: string;
  nationalite!: string;
  type_medecin!: any;
  date_fonction!: string;
  qualification!: string;
  nb_experience!: string;
  actif!: boolean;
  organismes!: string[];
  _identite!: string;
  enregistrer_par!: number;
  disabled: number;

  constructor(medecin: MedecinEntity) {
    this._id = medecin._id;
    this.code = medecin.code;
    this.prenom = medecin.prenom;
    this.nom = medecin.nom;
    this.disabled = medecin.disabled;
  }
}

export const MEDECIN_COLUMNS = () => {
  const router = useHistory();
  const localOrganismes = localStorage.getItem("organismes");
  const organismes = localOrganismes
    ? (JSON.parse(localOrganismes) as OrganismeEntity[])
    : [];

  return [
    {
      title: "Nom",
      key: "user-nom",
      render: (cell: any, row: any) => {
        return (
          <div>
            <span className="text-uppercase">{cell.nom + " "}</span>
            {cell.prenom}
          </div>
        );
      },
    },
    {
      title: "Adresse mail",
      key: "user-adresse_mail",
      render: (cell: any, row: any) => {
        return <span>{cell.adresse_mail}</span>;
      },
    },
    {
      title: "Telephone",
      key: "user-telephone",
      render: (cell: any, row: any) => {
        return <span>{row.telephone} </span>;
      },
    },
    {
      title: "Formations sanitaires",
      key: "user-formation_sanitaire",
      render: (cell: any, row: any) => {
        return (
          <div>
            <ul>
              {organismes.map((org) => {
                if (
                  row.organismes.includes(org.id) &&
                  org.type_organisme === CODE_RUBRIQUES.FORMATION_SANITAIRE
                ) {
                  return (
                    <li>
                      <Link
                        to={{
                          pathname: ROUTES.REGISTRE_PAGE.FORMATION_SANITAIRE,
                          state: {
                            formation_sanitaire: org.id,
                          },
                        }}
                      >
                        {org.designation}
                      </Link>
                    </li>
                  );
                }
              })}
            </ul>
          </div>
        );
      },
    },
    // {
    //   title: "Affiliation",
    //   key: "user-affiliation",
    //   render: (cell: any, row: any) => {
    //     return (
    //       <div>
    //         <ul>
    //           {organismes.map((org) => {
    //             if (
    //               row.organismes.includes(org.id) &&
    //               org.type_organisme === CODE_RUBRIQUES.RESEAU_TRAUMATOLOGIE
    //             ) {
    //               return <li>{org.designation}</li>;
    //             }
    //           })}
    //         </ul>
    //       </div>
    //     );
    //   },
    // },
    {
      title: "Dossiers",
      key: "user-dossiers",
      render: (cell: any, row: any) => {
        return (
          <Link
            to={{
              pathname: ROUTES.REGISTRE_PAGE.PRATICIEN,
              state: {
                praticien: row.id,
              },
            }}
            className="text-center"
          >
            <Badge
              color={row.statut === 2 ? "green" : "orange"}
              count={row.dossiers.length}
              title="Nombre de dossiers"
              showZero
              className="text-center"
            />
          </Link>
        );
      },
    },
    {
      title: "Statut",
      key: "user-statut",
      render: (cell: any, row: any) => {
        return <span> {UserStatutTag(row.statut)} </span>;
      },
    },
    {
      title: "Actions",
      key: "user-actions",
      render: (cell: any, row: any) => (
        <Space>
          <div style={{ cursor: "pointer" }}>
            {/* consulter les infos du praticien */}
            <FaInfoCircle
              size={24}
              color={GREY_LIGHT}
              onClick={() =>
                router.push({
                  pathname: ROUTES.USER_PAGE.SHOW_USER,
                  state: {
                    user: row,
                    readOnly: true,
                  },
                })
              }
              style={{ color: "#FF4D4F" }}
            />
          </div>
        </Space>
      ),
    },
  ];
};

const UserStatutTag = (val: any) => {
  switch (val) {
    case 0: // nouveau
      return (
        <Tag
          icon={<FaArrowDown size={12} />}
          color="processing"
          className="rounded-pill"
          bordered={false}
        >
          <span> nouveau </span>
        </Tag>
      );
    case 1: // en attente de validation
      return (
        <Tag
          icon={<FaClock size={12} />}
          color="processing"
          className="rounded-pill"
          bordered={false}
        >
          <span> en attente </span>
        </Tag>
      );
    case 2: // accepte
      return (
        <Tag
          icon={<FaCheck size={12} />}
          color="success"
          className="rounded-pill"
          bordered={false}
        >
          <span> accepte </span>
        </Tag>
      );
    case 3: // rejete
      return (
        <Tag
          icon={<FaWindowClose size={12} className="rounded-pill" />}
          color="error"
          className="rounded-pill"
          bordered={false}
        >
          <span> rejete </span>
        </Tag>
      );
    default:
      return (
        <Tag
          icon={<FaCheck size={12} />}
          color="default"
          className="rounded-pill"
          bordered={false}
        >
          <span> en attente </span>
        </Tag>
      );
  }
};
