import { Form, Space, Spin, notification } from "antd";
import {
  RubriqueTitle,
  RubriqueNavigate,
  FullSelectLayout,
  InputLayout,
  DateLayout,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";

import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";

export const FormHistoire = ({
  dossier,
  eltRubriques,
  activeKey,
  setActiveKey,
  reloadData,
}: {
  dossier: any;
  eltRubriques: EltRubriqueEntity[];
  activeKey: number;
  setActiveKey: (tag: number) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);

  const PREMIERS_SOINS = eltRubriques.filter(
    (elt) => elt.rubrique === CODE_RUBRIQUES.PREMIERS_SOINS
  );

  const DELAI_FS = eltRubriques.filter(
    (elt) => elt.rubrique === CODE_RUBRIQUES.DELAI_FS
  );

  const MOYENS_TRANSPORT = eltRubriques.filter(
    (elt) => elt.rubrique === CODE_RUBRIQUES.MOYENS_TRANSPORT
  );

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-histoire-form"
        key="acq-histoire-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.histoire ? "collapse show" : "d-none"
        }
        id="acq-histoire"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.enquete,
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DATE_TRAUMATISME,
                valeur: [new Date(data.hist_date).toLocaleString()],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.PREMIERS_SOINS,
                valeur: [data.hist_premier_soin],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.DELAI_FS,
                valeur: [data.hist_delai_fs],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.MOYENS_TRANSPORT,
                valeur: [data.hist_moyen_trans],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              setActiveKey(DOSSIER_STATUT.enquete);
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
              // set check icon to danger (non enregistr)
            }
          });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle
            key="dossier-acq-histoire"
            title="Histoire de la maladie"
          />
          <div className="row">
            <DateLayout
              label="Date du traumatisme"
              name="hist_date"
              key="hist_date"
              rules={[formRules.ruleNoRequired]}
              size={7}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.histoire,
                CODE_RUBRIQUES.DATE_TRAUMATISME,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.histoire ? true : false}
            />
            <FullSelectLayout
              label="Premiers soins par"
              name="hist_premier_soin"
              key="hist_premier_soin"
              items={PREMIERS_SOINS}
              size={7}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.histoire,
                CODE_RUBRIQUES.PREMIERS_SOINS,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.histoire ? true : false}
            />
            <FullSelectLayout
              label="Dlai d'arrive  la FS"
              name="hist_delai_fs"
              key="hist_delai_fs"
              items={DELAI_FS}
              size={7}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.histoire,
                CODE_RUBRIQUES.DELAI_FS,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.histoire ? true : false}
            />
            <FullSelectLayout
              label="Moyen de transport vers la FS"
              name="hist_moyen_trans"
              key="hist_moyen_trans"
              items={MOYENS_TRANSPORT}
              size={7}
              rules={[formRules.ruleNoRequired]}
              value={getPayload(
                dossier,
                DOSSIER_STATUT.histoire,
                CODE_RUBRIQUES.MOYENS_TRANSPORT,
                false
              )}
              disabled={dossier.statut > DOSSIER_STATUT.histoire ? true : false}
            />
          </div>
          {dossier.statut > DOSSIER_STATUT.histoire ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.motif)}
              >
                Precedent
              </button>
              <button
                type="button"
                className="btn btn-primary rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.enquete)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-histoire" />
          )}
        </div>
      </Form>
    </Spin>
  );
};
