import { Button, Form, Space, Spin, Upload, notification } from "antd";
import {
  RubriqueTitle,
  FullSelectLayout,
  RubriqueNavigate,
  InputLayout,
} from "../../../../shared/Layouts";
import { useState } from "react";
import { CODE_RUBRIQUES, DOSSIER_STATUT } from "../../../../../shared/rubrique";
import { completeDossier } from "../../../network";

import { FaImage, FaPlus } from "react-icons/fa";
import { PRIMARY } from "../../../../../shared/colors";
import { ButtonWithModal } from "../../../../shared/ButtonWithModal";
import { EltRubriqueEntity } from "../../../../../entities/eltRubrique.entity";
import { formRules } from "../../../../../shared/formRules";
import { getPayload } from "../DossierDetails";

export const FormExamen = ({
  dossier,
  eltRubriques,
  activeKey,
  setActiveKey,
  reloadData,
}: {
  dossier: any;
  eltRubriques: EltRubriqueEntity[];
  activeKey: number;
  setActiveKey: (tag: number) => void;
  reloadData: (val: boolean) => void;
}) => {
  const [form] = Form.useForm();
  const [isLoadingForm, setIsLoadingForm] = useState(false);

  const normFile = (e: any) => {
    if (Array.isArray(e)) {
      return e;
    }
    return e?.fileList;
  };

  return (
    <Spin spinning={isLoadingForm} className="row">
      <Form
        name="acq-examen-form"
        key="acq-examen-form"
        form={form}
        className={
          activeKey === DOSSIER_STATUT.examen ? "collapse show" : "d-none"
        }
        id="acq-examen"
        labelCol={{ span: 8 }}
        scrollToFirstError
        onFinish={async (data) => {
          setIsLoadingForm(true);
          let dataToSend: any = {
            dossierId: dossier.id,
            statut: DOSSIER_STATUT.diagnostic, // motif de la consultation <==> anamnese
            step: "1",
            eltDossiers: [
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_TA,
                valeur: [data.par_ta],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_POULS,
                valeur: [data.par_pouls],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_FR,
                valeur: [data.par_fr],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_T,
                valeur: [data.par_t],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_GENERAL,
                valeur: [data.exa_gen],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.LUXATION_CLINIQUE,
                valeur: data.lux_cli.length === 0 ? ["0000000"] : data.lux_cli,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.FRACTURE_CLINIQUE,
                valeur: data.fra_cli.length === 0 ? ["0000000"] : data.fra_cli,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.TYPE_FRACTURE,
                valeur: data.typ_fra.length === 0 ? ["0000000"] : data.typ_fra,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.TYPE_FRACTURE_PEDIATRIE,
                valeur:
                  data.typ_frac_ped.length === 0
                    ? ["0000000"]
                    : data.typ_frac_ped,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.COLORATION_MEMBRE,
                valeur: [data.exa_col],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.POULS_PERIPHERIQUE,
                valeur: [data.exa_pouls_per],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.TEMPERATURE_MEMBRE,
                valeur: [data.exa_temp_mem],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.SENSIBILITE,
                valeur: [data.exa_sens],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_APP_DIGESTIF,
                valeur: [data.exa_app_dig],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_APP_UROGENITAL,
                valeur: [data.exa_app_uro],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_APP_CARDIO,
                valeur: [data.exa_app_cardio],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_APP_NEURO,
                valeur: [data.exa_app_neuro],
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
              {
                dossier: dossier.id,
                rubrique: CODE_RUBRIQUES.EXAMEN_RADIO_INITIALES,
                valeur:
                  data.exa_rad_ini.length === 0
                    ? ["0000000"]
                    : data.exa_rad_ini,
                date_update: new Date().toLocaleString(),
                statut: 1,
              },
            ],
          };
          console.log("data to send in examen ", dataToSend);
          await completeDossier(dataToSend).then(async (data) => {
            if (data.success) {
              notification.success({
                message: "Succs",
                description: data.message,
              });
              reloadData(true);
              setActiveKey(DOSSIER_STATUT.diagnostic);
            } else {
              notification.error({
                message: "Erreur",
                description: data.message,
              });
            }
          });
          setIsLoadingForm(false);
        }}
      >
        <div className="card card-body">
          <RubriqueTitle key="dossier-acq-examen" title="Examen physique" />
          <div>
            {/* PARAMETRES */}
            <div className="row" id="exa_parametres">
              <strong>
                <h5
                  style={{ fontFamily: "monospace" }}
                  className="text-primary"
                >
                  Paramtres
                </h5>{" "}
                <hr />
              </strong>
              <InputLayout
                label="TA"
                name="par_ta"
                key="par_ta"
                className="col-md-3 mr-0"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_TA,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="Pouls"
                name="par_pouls"
                key="par_pouls"
                className="col-md-3"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_POULS,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="FR"
                size={12}
                name="par_fr"
                key="par_fr"
                className="col-md-3"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_FR,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="T"
                size={12}
                name="par_t"
                key="par_t"
                className="col-md-3"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_T,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
            </div>
            <div>
              <FullSelectLayout
                label="Examen Gneral / Extremit cphalique "
                key="exa_gen"
                name="exa_gen"
                size={12}
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.EXAMEN_GENERAL
                )}
                multiple={true}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_GENERAL,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
            </div>
            {/* APPAREIL LOCOMOTEUR */}
            <div className="row" id="exa_app-loc">
              <strong>
                <h5
                  style={{ fontFamily: "monospace" }}
                  className="text-primary"
                >
                  Appareil locomoteur
                </h5>{" "}
                <hr />
              </strong>
              <FullSelectLayout
                multiple={true}
                label="Luxation clinique de "
                key="lux_cli"
                name="lux_cli"
                size={12}
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.LUXATION_CLINIQUE
                )}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.LUXATION_CLINIQUE,
                  true
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                multiple={true}
                label="Fracture clinique de "
                key="fra_cli"
                name="fra_cli"
                size={12}
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.FRACTURE_CLINIQUE
                )}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.FRACTURE_CLINIQUE,
                  true
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                multiple={true}
                label="Type de fracture"
                key="typ_fra"
                name="typ_fra"
                size={12}
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.TYPE_FRACTURE
                )}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.TYPE_FRACTURE,
                  true
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                multiple={true}
                label="Type de fracture pdiatrique "
                key="typ_frac_ped"
                name="typ_frac_ped"
                size={12}
                items={eltRubriques.filter(
                  (elt) =>
                    elt.rubrique === CODE_RUBRIQUES.TYPE_FRACTURE_PEDIATRIE
                )}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.TYPE_FRACTURE_PEDIATRIE,
                  true
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
            </div>
            {/* COLORATION, POULS, TEMPERATURE, SENSIBILITE */}
            <div className="row" id="col-pls">
              <strong>
                <h5
                  style={{ fontFamily: "monospace" }}
                  className="text-primary"
                >
                  Autres paramtres
                </h5>
                <hr />
              </strong>
              <FullSelectLayout
                label="Coloration priphrique du membre"
                name="exa_col"
                key="exa_col"
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.COLORATION_MEMBRE
                )}
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.COLORATION_MEMBRE,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                label="Pouls priphriques"
                name="exa_pouls_per"
                key="exa_pouls_per"
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.POULS_PERIPHERIQUE
                )}
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.POULS_PERIPHERIQUE,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                label="Temprature du membre"
                name="exa_temp_mem"
                key="exa_temp_mem"
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.TEMPERATURE_MEMBRE
                )}
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.TEMPERATURE_MEMBRE,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <FullSelectLayout
                label="Sensibilit"
                name="exa_sens"
                key="exa_sens"
                items={eltRubriques.filter(
                  (elt) => elt.rubrique === CODE_RUBRIQUES.SENSIBILITE
                )}
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.SENSIBILITE,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
            </div>
            {/* Autres appareils */}
            <div className="row" id="exa_autres">
              <strong>
                <h5
                  style={{ fontFamily: "monospace" }}
                  className="text-primary"
                >
                  Examen des appareils
                </h5>{" "}
                <hr />
              </strong>
              <InputLayout
                label="Appareil digestif"
                multiple={true}
                size={12}
                name="exa_app_dig"
                key="exa_app_dig"
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_APP_DIGESTIF,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="Appareil Urognital"
                multiple={true}
                name="exa_app_uro"
                key="exa_app_uro"
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_APP_UROGENITAL,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="Appareil cardio vasculaire"
                multiple={true}
                name="exa_app_cardio"
                key="exa_app_cardio"
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_APP_CARDIO,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              <InputLayout
                label="Examen neurologique"
                multiple={true}
                name="exa_app_neuro"
                key="exa_app_neuro"
                size={12}
                rules={[formRules.ruleNoRequired]}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_APP_NEURO,
                  false
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
            </div>
            <div className="row mb-3">
              <FullSelectLayout
                multiple={true}
                size={12}
                label="Radiographies initiales"
                key="exa_rad_ini"
                name="exa_rad_ini"
                items={eltRubriques.filter(
                  (elt) =>
                    elt.rubrique === CODE_RUBRIQUES.EXAMEN_RADIO_INITIALES
                )}
                value={getPayload(
                  dossier,
                  DOSSIER_STATUT.examen,
                  CODE_RUBRIQUES.EXAMEN_RADIO_INITIALES,
                  true
                )}
                disabled={dossier.statut > DOSSIER_STATUT.examen ? true : false}
              />
              {/* Schma des fractures */}
              <div className="row mt-2">
                <strong>
                  <h5
                    style={{ fontFamily: "monospace" }}
                    className="text-primary"
                  >
                    Schma des fractures
                  </h5>
                  <hr />
                </strong>
                <label htmlFor="shma-fracture" className="col-md-8">
                  Schma des fractures pdiatriques / adultes
                </label>{" "}
                {/* Inserer l'image de la radiographie */}
                <div>
                  <Form.Item
                    label="Upload"
                    valuePropName="fileList"
                    getValueFromEvent={normFile}
                  >
                    <Upload action="/upload.do" listType="picture-card">
                      <div>
                        <FaPlus />
                        <div style={{ marginTop: 8 }}>Upload</div>
                      </div>
                    </Upload>
                  </Form.Item>
                  {/* <ButtonWithModal
                    buttonText={
                      <div className="">
                        <small className="col">Charger l'image</small>{" "}
                        <FaImage size={28} color={PRIMARY} />
                      </div>
                    }
                    buttonProps={{
                      danger: false,
                      className: "mx-auto",
                    }}
                    modalProps={{ title: "" }}
                  >
                    {(closeModal) => (
                      <div>
                        <h3>Charger l'image de la radio</h3>
                        <div className="card"></div>
                        <Space>
                          <Button type="primary" onClick={() => closeModal()}>
                            Fermer
                          </Button>
                          <Button
                            danger
                            onClick={() => {
                              notification.success({
                                message: "Succs",
                                description: "Laisse l'enseignement pass",
                              });
                            }}
                          >
                            Oui
                          </Button>
                        </Space>
                      </div>
                    )}
                  </ButtonWithModal> */}
                </div>
              </div>
            </div>
          </div>
          {dossier.statut > DOSSIER_STATUT.examen ? (
            <Space className="ms-auto">
              <button
                type="button"
                className="btn btn-outline-secondary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.enquete)}
              >
                Precedent
              </button>
              <button
                type="button"
                className="btn btn-primary ml-3 rounded-pill "
                onClick={() => setActiveKey(DOSSIER_STATUT.diagnostic)}
              >
                Suivant
              </button>
            </Space>
          ) : (
            <RubriqueNavigate key="nav-acq-examen" />
          )}{" "}
        </div>
      </Form>
    </Spin>
  );
};
